(function() {
  'use strict';

  /* Datatable services
   * Functions to build and to control rows and actions in data table */

  angular
    .module('loadsAppWeb')
    .service('DatatableTool', DatatableServices);

  /** @ngInject */
  function DatatableServices($q, $http, Config, $filter) {
	  var vm = this;

    vm.pageLength = 7;

  	vm.getDefaultSettings = function()
  	{
  		  var config = {
          searching: false,
          ordering: true,
          info: false,
          lengthChange: false
        };

        return config;
  	};

    vm.getDefaultLanguageSettings = function()
    {
      return {
        emptyTable: $filter('translate')('loads:list:empty:text'),
        processing: $filter('translate')('table:loading_data:text'),
        //processing: $compile('<loading-icon class="full-height full-height-absolute"></loading-icon>')($rootScope),
        paginate: {
          previous: $filter('translate')('table:pagination:prev:text'),
          next: $filter('translate')('table:pagination:next:text'),
          first: $filter('translate')('table:pagination:first:text'),
          last: $filter('translate')('table:pagination:last:text'),
        }
      };
    };

    vm.setLimit = function(data) {
      vm.pageLength = data;
    };

    vm.getLimit = function(data) {
      var limit = vm.pageLength;

      if (!angular.isUndefined(data) && data != null) {
          limit = data.length;
      }

      return limit;
    };

    /**
     * Returns the page for the tables
     * @param data
     */
    vm.getPage = function(data) {
        console.log(data);
        var page = 1;

        if (!angular.isUndefined(data)) {
            page = parseInt(data.start / vm.getLimit(data)) + 1;
        }

        return page;
    };

    vm.getRecords = function(data) {
        var records = {
            recordsTotal: 0,
            recordsFiltered: 0,
            data: []
        };

        if (data && !angular.isUndefined(data.total_results_system) && !angular.isUndefined(data.results)) {
            records.recordsTotal = data.total_results_system;
            records.recordsFiltered = data.total_results_system;
            records.data = data.results;
        }

        return records;
    };

    vm.getRecordsTrucks = function(data) {
        var records = {
            recordsTotal: 0,
            recordsFiltered: 0,
            data: []
        };

        if (data && !angular.isUndefined(data.truck_total_results) && !angular.isUndefined(data.truck_results)) {
            records.recordsTotal = data.truck_total_results;
            records.recordsFiltered = data.truck_total_results;
            records.data = data.truck_results;
        }

        return records;
    };

    vm.getRecordsDrivers = function(data) {
        var records = {
            recordsTotal: 0,
            recordsFiltered: 0,
            data: []
        };

        if (data && !angular.isUndefined(data.list_drivers)) {
            records.recordsTotal = data.list_drivers.length;
            records.recordsFiltered = data.list_drivers.length;
            records.data = data.list_drivers;
        }

        return records;
    };

    /**
     * Returns an instance for datatables.
     */
    vm.getInstance = function() {
        // Instance
        var instance = null;

        // Tables
        var tables = $.fn.dataTable.tables(true);
        if (angular.isArray(tables) && tables.length > 0) {
            instance = $(tables[0]).DataTable();
        }

        return instance;
    };

    /**
     * Reload the datatables
     */
    vm.reload = function() {
        var table = vm.getInstance();

        if (table) {
            var callback = null,
                resetPaging = true;
            table.ajax.reload(callback, resetPaging);
        }
    };
  }
})();

(function() {
  'use strict';

  /* Capitalize function */

  angular
    .module('loadsAppWeb')
    .filter('capitalize', Capitalize);

  /** @ngInject */
  function Capitalize() {
    return function(input) {
        return (input) ? input.charAt(0).toUpperCase() + input.substr(1).toLowerCase() : '';
    }
  }
})();

(function() {
  'use strict';

  /* Looking for loads modal controller
   * Set searching form vars 
   * Get load attributes 
   * Redirect to loads view with searching params
   */

  angular
    .module('loadsAppWeb')
    .controller('SearchLoadModalController', SearchLoadModalController);

  /** @ngInject */
  function SearchLoadModalController($state, $uibModalInstance, $filter, Loads) {
	  var vm = this;
      vm.from_any = false;
      vm.to_any = false;
      vm.price_any = false;
      vm.searchData = {};
      vm.searchData.fromObject = null;
      vm.searchData.from = null;
      vm.searchData.toObject = null;
      vm.searchData.to = null;
      vm.searchData.pickup = null;
      vm.searchData.delivery = null;
      vm.searchData.price_min = null;
      vm.searchData.price_max = null;
      vm.searchData.length = null;
      vm.searchData.size = null;
      vm.searchData.weight = null;
      vm.searchData.load_type = null;
      vm.searchData.load_id = null;
      vm.searchData.deadhead = null;
      vm.searchData.shipper = null;
      vm.searchData.review = 0;

      vm.advancedOptions = false;

      vm.dateDeliveryDisabled = true;
      vm.datePickerMinDate = moment();
      vm.optsDatePicker = {
          singleDatePicker: true,
          showDropdowns: true,
          locale: {
              format: "YYYY-MM-DD",
              applyClass: 'btn-green',
              applyLabel: $filter('translate')('daterangepicker:btn:success:text'),
              fromLabel: $filter('translate')('daterangepicker:label:from'),
              toLabel: $filter('translate')('daterangepicker:label:to'),
              cancelLabel: $filter('translate')('daterangepicker:btn:cancel:text'),
              customRangeLabel: $filter('translate')('daterangepicker:title:text'),
              firstDay: 1
          }
        };

      vm.priceMaxDisabled = true;

      /* if user selects any option in "date from", set vars as null */
      vm.fromAnyChanged = function() {
        if(vm.from_any){
          vm.searchData.fromObject = null;
          vm.searchData.from = null;
        }
      };

      /* if user selects any option in "date to", set vars as null */
      vm.toAnyChanged = function() {
        if(vm.to_any){
          vm.searchData.toObject = null;
          vm.searchData.to = null;
        }
      };

      /* if user selects any option in "price", set vars as null */
      vm.priceAnyChanged = function() {
        if(vm.price_any){
          vm.searchData.price_min = 0;
          vm.searchData.price_max = 0;
        }
      };

      /* Validate form and redirect to loads view with params */
      vm.sendDataFormSearch = function() {
        if(!vm.from_any){
          if(vm.searchData.fromObject != null){
            if(angular.isDefined(vm.searchData.fromObject.address_components)){
              vm.searchData.from = vm.searchData.fromObject.address_components[0].long_name;
            }
          }
        }else{
          vm.searchData.from = 0;
        }

        if(!vm.to_any){
          if(vm.searchData.toObject != null){
            if(angular.isDefined(vm.searchData.toObject.address_components)){
              vm.searchData.to = vm.searchData.toObject.address_components[0].long_name;
            }
          }
        }else{
          vm.searchData.to = 0;
        }

        if(vm.searchData.review == 0){
          vm.searchData.review = null;
        }
      	$state.go('private.loads.params', { from: vm.searchData.from, to: vm.searchData.to, pickup: vm.searchData.pickup, delivery: vm.searchData.delivery, price_min: vm.searchData.price_min, price_max: vm.searchData.price_max, length: vm.searchData.length, size: vm.searchData.size, weight: vm.searchData.weight, load_type: vm.searchData.load_type, load_id: vm.searchData.load_id, shipper: vm.searchData.shipper, review: vm.searchData.review, deadhead: vm.searchData.deadhead});
      	vm.closePopup();
      };

    /* Close modal */
      vm.closePopup = function() {
      	$uibModalInstance.close();
	  };

    /* Validate pickup date and allow to set delivery date */
    vm.allowDeliveryDate = function() {
      if(vm.searchData.pickup != null && vm.searchData.pickup != ""){
        vm.dateDeliveryDisabled = false;
        vm.searchData.delivery = null;
        /*if(vm.searchData.delivery < vm.searchData.pickup){
          vm.searchData.delivery = vm.searchData.pickup;
        }*/
      }else{
        vm.dateDeliveryDisabled = true;
        vm.searchData.delivery = null;
      }
    };

    /* Validate price min and allow to set price max */
    vm.allowPriceMax = function() {
      if(vm.searchData.price_min != null && vm.searchData.price_min >= 0){
        vm.priceMaxDisabled = false;
        if(vm.searchData.price_max < vm.searchData.price_min){
          vm.searchData.price_max = vm.searchData.price_min;
        }
      }else{
        vm.priceMaxDisabled = true;
        vm.searchData.price_max = null;
      }
    };

    /* Advanced filters toogle */
    vm.showHideAdvancedFilters = function(){
      vm.advancedOptions = !vm.advancedOptions;

      if(!vm.advancedOptions){
        vm.resetFilters('advanced');
      }
    };

    /* Reset filters */
    vm.resetFilters = function(mode){
      switch(mode){
        case 'advanced':
          vm.from_any = false;
          vm.to_any = false;
          vm.price_any = false;
          vm.searchData.price_min = null;
          vm.searchData.price_max = null;
          vm.searchData.length = null;
          vm.searchData.size = null;
          vm.searchData.weight = null;
          vm.searchData.load_type = null;
          vm.searchData.load_id = null;
          vm.searchData.shipper = null;
          vm.searchData.review = 0;

          vm.priceMaxDisabled = true;
          break;
        default:
          vm.from_any = false;
          vm.to_any = false;
          vm.price_any = false;
          vm.searchData.fromObject = null;
          vm.searchData.from = null;
          vm.searchData.toObject = null;
          vm.searchData.to = null;
          vm.searchData.pickup = null;
          vm.searchData.delivery = null;
          vm.searchData.price_min = null;
          vm.searchData.price_max = null;
          vm.searchData.length = null;
          vm.searchData.size = null;
          vm.searchData.weight = null;
          vm.searchData.load_type = null;
          vm.searchData.shipper = null;
          vm.searchData.load_id = null;
          vm.searchData.review = 0;

          vm.dateDeliveryDisabled = true;
          vm.datePickerMinDate = moment();
          vm.priceMaxDisabled = true;
          break;
      }
    };

    /* Validate review selected by user */
    vm.setReview = function(ind){
      ind = parseInt(ind);
      if(ind >=0 && ind <= 5){
        vm.searchData.review = ind;
      }
    };

    /* Get load attributes */
    vm.getItemsSizeLengthLoads = function(){
      vm.itemsSizeLenLoads = {};
      Loads.getItemsSizeLengthLoads()
        .then(function(response){
          if(response){
            vm.itemsSizeLenLoads = response;
          }
        });
    };

    vm.getItemsSizeLengthLoads();
  }
})();

(function() {
  'use strict';

  /* Looking for carrier modal controller
   * Set searching form vars 
   * Get load attributes and truck skills list
   * Redirect to carriers view with searching params
   */

  angular
    .module('loadsAppWeb')
    .controller('SearchCarrierModalController', SearchCarrierModalController);

  /** @ngInject */
  function SearchCarrierModalController($state, $uibModalInstance, Loads) {
	  var vm = this;
      vm.searchData = {};
      vm.searchData.company = null;
      vm.searchData.trailer_type = null;
      vm.searchData.len = null;
      vm.searchData.wei = null;
      vm.searchData.size = null;
      vm.searchData.reviews = 0;
      vm.searchData.skils_truck = [];

      /* Redirect to carriers view with params */
      vm.sendDataFormSearch = function() {
        if(angular.isDefined(vm.skillList)){
          vm.skillList.forEach(function(item) {
            if (item.checked) vm.searchData.skils_truck.push(item.id);
          });
        }

      	$state.go('private.carriers.params', { company: vm.searchData.company, trailer_type: vm.searchData.trailer_type, len: vm.searchData.len, wei: vm.searchData.wei, size: vm.searchData.size, skils_truck: vm.searchData.skils_truck, reviews: vm.searchData.reviews });
      	vm.closePopup();
      };

      /* Close modal */
      vm.closePopup = function() {
      	$uibModalInstance.close();
	    };

      /* Get load attributes */
      vm.getItemsSizeLengthLoads = function(){
        vm.itemsSizeLenLoads = {};
        Loads.getItemsSizeLengthLoads()
          .then(function(response){
            if(response){
              vm.itemsSizeLenLoads = response;
              console.log(vm.itemsSizeLenLoads);
              if(angular.isDefined(vm.itemsSizeLenLoads.TrucksSkills_list)){
                vm.setTruckSkillList();
              }
            }
          });
      };

      /* Set truck skills list in local var */
      vm.setTruckSkillList = function() {
        vm.skillList = [];
        if(vm.itemsSizeLenLoads.TrucksSkills_list.length){
          for(var i in vm.itemsSizeLenLoads.TrucksSkills_list){
            vm.skillList.push({id: vm.itemsSizeLenLoads.TrucksSkills_list[i].id, name: vm.itemsSizeLenLoads.TrucksSkills_list[i].skills_name, checked: false});
          }
        }
      };

      /* Set review when it is selected */
      vm.setReview = function(ind){
        ind = parseInt(ind);
        if(ind >=0 && ind <= 5){
          vm.searchData.reviews = ind;
        }
      };

      /* Reset form */
      vm.resetDataFormSearch = function(){
        vm.searchData.company = null;
        vm.searchData.trailer_type = null;
        vm.searchData.len = null;
        vm.searchData.wei = null;
        vm.searchData.size = null;
        vm.searchData.reviews = 0;
        vm.searchData.skils_truck = [];

        vm.setTruckSkillList();
      };

      vm.getItemsSizeLengthLoads();
  }
})();

