(function() {
  'use strict';

  /* Invite to join dropdown Directive
   * Dropdown that is at header */

  angular
    .module('loadsAppWeb')
    .directive('inviteJoin', InviteJoin);

  /** @ngInject */
  function InviteJoin($rootScope, Auth, $filter, Config, toastr) {
    return {
        templateUrl: 'app/main/templates/directives/invite-join.html',
        restrict: 'E',
        replace: true,
        scope: {
          type: '='
        },
        link: function(scope) {
            /**
             * Initialize
             */
            scope.initialize = function() {
              scope.dataSend = {}
              scope.dataSend.emailsString = '';
            };

            scope.sendMailInvitation = function(inviteJoinFormName){
              if (inviteJoinFormName.$invalid) {
                  // If there was an error for required fields.
                  if (inviteJoinFormName.$error.required) {
                      toastr.error($filter('translate')('header:invite_join:form:required:fields:error'));
                  } else {
                      toastr.error($filter('translate')('header:invite_join:form:required:fields:invalid'));
                  }
                  return;
              }
              var subject = $filter('translate')('header:invite_join:mail:send:subject')
              , body = $filter('translate')('header:invite_join:mail:send:body') + " " + Config.ENV.INVITE_JOIN_MAIL_BODY_URL;
              window.location.href="mailto:"+scope.dataSend.emailsString+"?subject="+subject+"&body="+body;
            };

            scope.shareFacebook = function() {
              FB.ui({
                method: 'share',
                href: Config.ENV.INVITE_JOIN_MAIL_BODY_URL,
                quote: $filter('translate')('header:invite_join:mail:send:body') + " " + Config.ENV.INVITE_JOIN_MAIL_BODY_URL
              }, function(response){});
            };

            scope.shareTwitter = function() {
              var title = $filter('translate')('header:invite_join:mail:send:subject')
              , body = $filter('translate')('header:invite_join:mail:send:body') + " " + Config.ENV.INVITE_JOIN_MAIL_BODY_URL;
              window.open('http://twitter.com/share?url='+Config.ENV.INVITE_JOIN_MAIL_BODY_URL+'&text='+body, '', 'left=0,top=0,width=550,height=450,personalbar=0,toolbar=0,scrollbars=0,resizable=0');
            };


            // Initialize
            scope.initialize();
        }
    };
  }
})();

(function() {
  'use strict';

  /* Header Directive
   * Left panel where is main menu */

  angular
    .module('loadsAppWeb')
    .directive('appHeader', AppHeader);

  /** @ngInject */
  function AppHeader($rootScope, Auth, $state, $uibModal, Notification, $timeout) {
    return {
        templateUrl: 'app/main/templates/directives/app-header.html',
        restrict: 'E',
        replace: true,
        scope: {},
        link: function(scope) {
            /**
             * Initialize
             */
            scope.initialize = function() {
                scope.notificationTypeList = "widget";
                scope.profile = Auth.getUserProfile();
                scope.user = Auth.getUserData();
                scope.userDataProfile = Auth.getUserDataProfile();
                scope.notificationsCount = '';

                if(scope.profile){
                  scope.profile = scope.profile.toLowerCase();
                }
                //scope.appTitle = angular.copy($rootScope.appTitle);

                scope.getInfoNotifications();
            };

            /* Get notification list and set new notifications count */
            scope.getInfoNotifications =  function() {
              scope.notifOptions = {
                'token': Auth.getUserToken(),
                'page': 1
              };

              Notification.getNotifications(scope.notifOptions)
                .then(function(response){
                  if(angular.isDefined(response.notif_total_results)){
                    if(response.notif_total_results > 0){
                      var countNotifAux = 0;
                      scope.notificationsCount = '';
                      for(var i in response.notif_results){
                        if(response.notif_results[i].is_read == 'N'){
                          countNotifAux++;
                        }
                      }
                      
                      scope.notificationsCount = (countNotifAux > 0) ? countNotifAux : '';
                    }
                  }
                });
            };

            /* Logout option */
            scope.logout = function() {
              Auth.logout();
              $state.go('public.login');
            };

            /* Call carrier searching modal */
            scope.openSearchCarriersModal = function() {
              var modalInstance = $uibModal.open({
                controller: 'SearchCarrierModalController as searchcarrier',
                windowClass: 'search-carrier-modal',
                templateUrl: 'app/main/templates/modal/search-carrier.html'
              });
            };

            /* Call loads searching modal */
            scope.openSearchLoadsModal = function() {
              var modalInstance = $uibModal.open({
                controller: 'SearchLoadModalController as searchload',
                windowClass: 'search-load-modal',
                templateUrl: 'app/main/templates/modal/search-load.html'
              });
            };

            /* Redirect to profile page */
            scope.goToProfile = function(){
              $state.go('private.my_profile');
            };

            /* Set new notifications as read */
            scope.setReadNotifications = function(){
              if(!angular.element('#notify-header-item-dropdown').hasClass('show')){
                $timeout(function () {
                  var params = {};
                  params.token = Auth.getUserToken();
                  Notification.setReadNotifications(params)
                    .then(function(response){
                      scope.getInfoNotifications();
                    });
                }, 2000);
              }
            }

            // Initialize
            scope.initialize();
        }
    };
  }
})();

(function() {
  'use strict';

  /* Aside Directive
   * Left panel where is main menu */

  angular
    .module('loadsAppWeb')
    .directive('appAside', AppAside);

  /** @ngInject */
  function AppAside($state, $rootScope, Auth) {
    return {
        templateUrl: 'app/main/templates/directives/app-aside.html',
        restrict: 'E',
        replace: true,
        scope: {},
        link: function(scope) {
            /**
             * Initialize
             */
            scope.initialize = function() {
                scope.profile = Auth.getUserProfile();
                scope.loadsActive = $state.includes('private.loads');
                scope.carriersActive = $state.includes('private.carriers');

                if(scope.profile){
                  scope.profile = scope.profile.toLowerCase();
                }
            };

            // Initialize
            scope.initialize();
        }
    };
  }
})();

