(function() {
  'use strict';

  /* Load details Directive
   * It shows load info in dropdown of trucks table list
   * It is a generic element is loaded when user click in dropdown button of item */

  angular
    .module('loadsAppWeb')
    .directive('loadDetailsRow', loadDetailsRow);

  /** @ngInject */
  function loadDetailsRow($rootScope, Auth, Loads, uiGmapGoogleMapApi, uiGmapIsReady, $uibModal, $filter) {
    return {
        templateUrl: 'app/loads/templates/directives/load-details-row.html', // directive template
        restrict: 'E',
        replace: true,
        scope: {
          loadId: '=', //Input of directive, Load id
          reloadParent: '&', //Input of directive, Funcion from loads table which reload items of the table
          posteverywhere: '=' //Input of directive, posteverywhere info
        },
        link: function(scope) {
            scope.loadingMap = true;
            scope.loadData = {};
            scope.loadingLoadData = true;
            scope.loadInvoiceImgFilesList = [];
            scope.loadInvoiceDocsFilesList = [];
            scope.loadLegalImgFilesList = [];
            scope.loadLegalDocsFilesList = [];
            scope.legalDocFileSaving = false;
            scope.userDataProfile = Auth.getUserDataProfile();
            scope.userProfile = Auth.getUserProfile();
            /**
             * Initialize
             */
            scope.initialize = function() {
                scope.loadOptions = {
                  'token': Auth.getUserToken(),
                  'load_id': scope.loadId
                };

                scope.getLoadDetails();
            };

            /* Load the map and set load coordinates */
            scope.initializeMap = function(){
              if(angular.isDefined(scope.loadData.from_lat) && scope.loadData.from_lat != "" && angular.isDefined(scope.loadData.from_long) && scope.loadData.from_long != "" && angular.isDefined(scope.loadData.to_lat) && scope.loadData.to_lat != "" && angular.isDefined(scope.loadData.to_long) && scope.loadData.to_long != ""){
                uiGmapGoogleMapApi.then(function(maps) {
                 scope.loadingMap = false;
                 scope.map = { center: { latitude: scope.loadData.from_lat, longitude: scope.loadData.from_long }, zoom: 14 };
                 scope.markers = [
                                  {id: 111, coords: { latitude: scope.loadData.from_lat, longitude: scope.loadData.from_long}, options: {} },
                                  {id: 222, coords: { latitude: scope.loadData.to_lat, longitude: scope.loadData.to_long}, options: {} }];

                 if(angular.isDefined(scope.loadData.info_truck)){
                    if(angular.isDefined(scope.loadData.info_truck.latitud_truck) && angular.isDefined(scope.loadData.info_truck.longitud_truck)){
                      scope.markers.push({id: 333, coords: {latitude: scope.loadData.info_truck.latitud_truck, longitude: scope.loadData.info_truck.longitud_truck}, options: { icon: "assets/images/common/truck-marker.png"} });
                    }
                  }
                });

                uiGmapIsReady.promise(1).then(function(maps) {
                  google.maps.event.trigger(maps, 'resize');
                });
              }
            };

            /* Get load info */
            scope.getLoadDetails =  function() {
              Loads.getLoadDetails(scope.loadOptions)
                .then(function(response){
                  scope.loadData = response.result;
                  if(angular.isDefined(scope.loadData.load_documents) && scope.loadData.load_documents.length){
                    var arr_img = '';
                    var elements_img = '';
                    for(var f_index in scope.loadData.load_documents){
                      if(scope.loadData.load_documents[f_index].type_file == 'pdf'){
                        elements_img = scope.loadData.load_documents[f_index].invoice_file.split("/");

                        if(scope.loadData.load_documents[f_index].invoice_type == 'legal_doc'){
                          scope.loadLegalDocsFilesList.push({
                            id: f_index + 1,
                            url: scope.loadData.load_documents[f_index].invoice_file, 
                            title: elements_img[elements_img.length - 1],
                            invoice_name: scope.loadData.load_documents[f_index].invoice_name
                          });
                        }else{
                          scope.loadInvoiceDocsFilesList.push({
                            id: f_index + 1,
                            url: scope.loadData.load_documents[f_index].invoice_file, 
                            title: elements_img[elements_img.length - 1],
                            invoice_name: scope.loadData.load_documents[f_index].invoice_name
                          });
                        }
                      }else{
                        arr_img = $.parseJSON('['+scope.loadData.load_documents[f_index].invoice_file+']');
                        elements_img = arr_img[0][0].normal.split("/");

                        if(scope.loadData.load_documents[f_index].invoice_type == 'legal_doc'){
                          scope.loadLegalImgFilesList.push({
                            id: f_index + 1,
                            url: arr_img[0][0].normal, 
                            invoice_name: elements_img[elements_img.length - 1],
                            title: scope.loadData.load_documents[f_index].invoice_name
                          });
                        }else{
                          scope.loadInvoiceImgFilesList.push({
                            id: f_index + 1,
                            url: arr_img[0][0].normal, 
                            invoice_name: elements_img[elements_img.length - 1],
                            title: scope.loadData.load_documents[f_index].invoice_name
                          });
                        }
                      }
                    }
                  }
                  if(angular.isDefined(scope.loadData.status_loads)){
                    var bgColorBtn = ''
                    , colorBtn = '';
                    switch(scope.loadData.status_loads){
                      case 'NOT_ASSIGNED':
                        bgColorBtn = 'bg-color-7';
                        colorBtn = 'color-armadillo';
                        break;
                      case 'ACEPTADA':
                        bgColorBtn = 'bg-color-2';
                        colorBtn = 'color-armadillo';
                        break;
                      case 'EN_TRANSITO':
                        bgColorBtn = 'bg-color-5';
                        colorBtn = 'color-armadillo';
                        break;
                      case 'ENTREGADA': 
                        bgColorBtn = 'bg-color-3';
                        colorBtn = 'color-armadillo';
                        break;
                      default:
                        bgColorBtn = 'bg-color-7';
                        colorBtn = 'color-armadillo';
                        break;
                    }

                    scope.loadData.status_load_btnbg = bgColorBtn;
                    scope.loadData.status_load_btncolor = colorBtn;
                    scope.loadData.status_load_btntext = $filter('translate')('loads:table:status:' +scope.loadData.status_loads.toLowerCase()+ ':text');
                  }
                  if(angular.isDefined(scope.loadData.type_load) && scope.loadData.type_load.length){
                    var arr_typeload = [];
                    for(var i in scope.loadData.type_load){
                      arr_typeload.push(scope.loadData.type_load[i].skill_name);
                    }
                    scope.loadData.type_load_text = arr_typeload.join(', ');
                  }else{
                    scope.loadData.type_load_text = "";
                  }
                  if(angular.isDefined(scope.loadData.skills_required_carrier) && scope.loadData.skills_required_carrier.length){
                    var arr_carrier_skills_required = [];
                    for(var i in scope.loadData.skills_required_carrier){
                      arr_carrier_skills_required.push(scope.loadData.skills_required_carrier[i].skills_required_name);
                    }
                    scope.loadData.carrier_skills_required_text = arr_carrier_skills_required.join(', ');
                  }else{
                    scope.loadData.carrier_skills_required_text = "";
                  }

                  scope.loadData.price_to_show = scope.loadData.price;
                  if(scope.loadData.posteverywhere == "1" && scope.loadData.price == 0){
                    scope.loadData.price_to_show = $filter('translate')('loads:table:price:call:text');
                  }
                  scope.initializeMap();
                  scope.verifyOptionsDropdown();
                  scope.loadingLoadData = false;
                })
                .catch(function (error) {
                  scope.loadingLoadData = false;
                });
            };

            /* Call Carrier request list modal */
            scope.openCarrierRequestModal = function() {
              var modalInstance = $uibModal.open({
                controller: 'CarrierRequestsModalController as carrierrequests',
                windowClass: 'carrier-requests-modal',
                templateUrl: 'app/loads/templates/modal/carrier-requests.html',
                resolve: {
                    load: function() {
                        return scope.loadId;
                    }
                }
              });

              modalInstance.result.then(function() {
                  scope.loadingLoadData = true;
                  scope.initialize();
              });
            };

            /* Call Carrier assigned info modal */
            scope.openCarrierAssignedInfoModal = function() {
              var modalInstance = $uibModal.open({
                controller: 'CarrierAssignedInfoModalController as carrierassignedinfo',
                windowClass: 'carrier-assigned-info-modal',
                templateUrl: 'app/loads/templates/modal/carrier-assigned-info.html',
                resolve: {
                    carrier: function() {
                        return scope.loadData.carrier;
                    }
                }
              });
            };

            /* Call Load activity info modal */
            scope.openLoadActivityInfoModal = function() {
              var modalInstance = $uibModal.open({
                controller: 'LoadActivityInfoModalController as loadactivityinfo',
                windowClass: 'load-activity-info-modal',
                templateUrl: 'app/loads/templates/modal/load-activity-info.html',
                resolve: {
                    load: function() {
                        return scope.loadData;
                    }
                }
              });
            };

            /* Call Truck assigned info modal */
            scope.openTruckAssignedInfoModal = function() {
              var modalInstance = $uibModal.open({
                controller: 'TruckAssignedInfoModalController as truckassignedinfo',
                windowClass: 'truck-assigned-info-modal',
                templateUrl: 'app/loads/templates/modal/truck-assigned-info.html',
                resolve: {
                    truck: function() {
                        return scope.loadData.info_truck;
                    }
                }
              });
            };

            /* Call Truck list to assign modal */
            scope.openChooseTruckModal = function() {
              var modalInstance = $uibModal.open({
                controller: 'LoadTruckSelectModalController as loadtruckselect',
                windowClass: 'load-truck-select-modal',
                templateUrl: 'app/loads/templates/modal/load-truck-select.html',
                resolve: {
                    load: function() {
                        return scope.loadId;
                    },
                    carrier: function() {
                        return scope.loadData.carrier;
                    }
                }
              });

              modalInstance.result.then(function() {
                  scope.loadingLoadData = true;
                  scope.initialize();
              });
            };

            /* Call Report issues modal */
            scope.openReportIssuesLoadModal = function() {
              var modalInstance = $uibModal.open({
                controller: 'ReportIssuesLoadModalController as reportissuesload',
                windowClass: 'report-issues-load-modal',
                templateUrl: 'app/loads/templates/modal/report-issues-load.html',
                resolve: {
                    load: function() {
                        return scope.loadId;
                    }
                }
              });
            };

            /* Call cancel load by shipper modal */
            scope.openCancelLoadModal = function() {
              var modalInstance = $uibModal.open({
                controller: 'CancelLoadModalController as cancelload',
                windowClass: 'cancel-load-modal',
                templateUrl: 'app/loads/templates/modal/cancel-load.html',
                resolve: {
                    load: function() {
                        return scope.loadId;
                    }
                }
              });

              modalInstance.result.then(function(data) {
                if(data === true){
                  scope.reloadParent();
                }
              });
            };

            /* Call Apply to load by carrier modal */
            scope.createRequestCarrierLoad = function() {
              Loads.createRequestCarrierLoad(scope.loadOptions)
                .then(function(response){
                  if(angular.isDefined(response.request_created)){
                    if(angular.isDefined(response.request_created.idrequest_loads)){
                      scope.loadingMap = true;
                      scope.loadingLoadData = true;
                      scope.getLoadDetails();
                    }
                  }
                });
            };

            /* Call Cancel my request to apply to the load modal */
            scope.cancelRequestCarrierLoad = function() {
              Loads.cancelRequestCarrierLoad(scope.loadOptions)
                .then(function(response){
                  scope.loadingMap = true;
                  scope.loadingLoadData = true;
                  scope.getLoadDetails();
                });
            };

            /* Validate if dropdown is able to show depending on user profile*/
            scope.verifyOptionsDropdown = function() {
              scope.allowDropdown = false;
              var profile = Auth.getUserProfile();

              switch(profile){
                case 'CARRIER':
                case 'OWNER':
                  if(scope.loadData.status_loads =='ACEPTADA' || scope.loadData.status_loads =='EN_TRANSITO'){
                    scope.allowDropdown = true;
                  }
                  
                  break;
                case 'GENERADOR_CARGA':
                  if(scope.loadData.status_loads !='CANCELADA'){
                    scope.allowDropdown = true;
                  }
                  break;
              }
            };

            /* Upload images and documents to the load */
            scope.uploadLegalDoc = function(file, invalidFiles) {
                  // Upload logo image.
                  //console.log(file);
                  if (file) {
                      // Loading
                      scope.legalDocFileSaving = true;
                      var nameFile = scope.loadId+'_'+moment().milliseconds();

                      if(file.name){
                        var nameFileArr = file.name;
                        nameFileArr = nameFileArr.split(".");
                        nameFile = nameFileArr[0];
                      }

                      // Upload
                      var params = {
                          token: Auth.getUserToken(),
                          invoice_name: nameFile,
                          load_id: scope.loadId,
                          invoice_type: 'legal_doc',
                          pic: file
                      };
                      Loads.uploadInvoiceToLoad(params)
                          .then(function(response) {
                              // Success
                              var elements_invoice = '';
                              if(angular.isDefined(response.invoice_name) && response.invoice_name.length > 0){
                                if(response.type_file =='pdf'){
                                  elements_invoice = response.invoice_name.split("/");
                                  scope.loadLegalDocsFilesList.push({url: response.invoice_name, name: elements_invoice[elements_invoice.length - 1]});
                                }else{
                                  elements_invoice = response.invoice_name[0].normal.split("/");
                                  scope.loadLegalImgFilesList.push({url: response.invoice_name[0].normal, name: elements_invoice[elements_invoice.length - 1]});
                                }   
                              }

                              // Loading
                              scope.legalDocFileSaving = false;
                          })
                          .catch(function(error) {
                              // Error
                              console.log(error);
                              // Loading
                              scope.legalDocFileSaving = false;
                          });
                  }
              };

            // Initialize
            scope.initialize();
        }
    };
  }
})();

(function() {
  'use strict';

  /* Truck assigned info modal controller
   * Get truck id 
   * Look for truck info
   * Redirect to loads by truck
   */

  angular
    .module('loadsAppWeb')
    .controller('TruckAssignedInfoModalController', TruckAssignedInfoModalController);

  /** @ngInject */
  function TruckAssignedInfoModalController($state, truck, Auth, Trucks, $uibModalInstance, $q, toastr, $filter) {
	  var vm = this;

    /* Function executed at strart, get truck id and look for truck info  */
    vm.initialize = function() {
      vm.truckInfoReceived = truck;
      vm.params = {};
      vm.params.id = null;
      vm.params.token = Auth.getUserToken();
      vm.finalTruckData = false;
      vm.loadingInfo = true;

      if(angular.isDefined(vm.truckInfoReceived.id)){
        vm.params.id =  vm.truckInfoReceived.id;

        vm.getTruckSearch();
      }else{
        vm.loadingInfo = false;
      }
    };

    /* Get truck info */
    vm.getTruckSearch = function() {

      Trucks.trucksDetails(vm.params)
        .then(function(response){
          console.log(response);
          vm.finalTruckData = response.Truck_Details[0];

          if(vm.finalTruckData){
            if(angular.isDefined(vm.finalTruckData.skills_truck) && vm.finalTruckData.skills_truck.length){
              var arr_skills = [];
              for(var sk in vm.finalTruckData.skills_truck){
                arr_skills.push(vm.finalTruckData.skills_truck[sk].skills_name);
              }
              vm.finalTruckData.SkillsTruckText = arr_skills.join(', ');
            }else{
              vm.finalTruckData.SkillsTruckText = "---";
            }

            if(angular.isDefined(vm.finalTruckData.truck_availability) && vm.finalTruckData.truck_availability.length){
              var arr_availability = [];
              for(var av in vm.finalTruckData.truck_availability){
                arr_availability.push(vm.finalTruckData.truck_availability[av].availablity_name);
              }
              vm.finalTruckData.AvailabilityTruckText = arr_availability.join(', ');
            }else{
              vm.finalTruckData.AvailabilityTruckText = "---";
            }
          }

          vm.loadingInfo = false;
        })
        .catch(function (error) {
          scope.loadingInfo = false;
        });
    };

    /* Redirect to loads by truck information */
    vm.goToTruckLoads = function() {
      $state.go('private.trucks.loads', {id: vm.finalTruckData.id});
      vm.closePopup();
    };

    /* Close popup */
    vm.closePopup = function() {
        $uibModalInstance.close();
    };

    vm.initialize();
    
  }
})();

(function() {
  'use strict';

  /* Load log searching modal controller
   * Set form data filters 
   * Send form data to API
   */

  angular
    .module('loadsAppWeb')
    .controller('SearchLoadLogModalController', SearchLoadLogModalController);

  /** @ngInject */
  function SearchLoadLogModalController($state, $uibModalInstance, $filter, Loads, toastr) {

    /* Set form vars  */
	  var vm = this;
      vm.searchData = {};
      vm.searchData.shortcut = 'custom';
      vm.searchData.date_from = null;
      vm.searchData.date_to = null;
      vm.searchData.load_id = null;

      vm.dateToDisabled = true;
      vm.datePickerMinDate = moment();
      vm.optsDatePicker = {
          singleDatePicker: true,
          showDropdowns: true,
          locale: {
              format: "YYYY-MM-DD",
              applyClass: 'btn-green',
              applyLabel: $filter('translate')('daterangepicker:btn:success:text'),
              fromLabel: $filter('translate')('daterangepicker:label:from'),
              toLabel: $filter('translate')('daterangepicker:label:to'),
              cancelLabel: $filter('translate')('daterangepicker:btn:cancel:text'),
              customRangeLabel: $filter('translate')('daterangepicker:title:text'),
              firstDay: 1
          }
        };

      vm.shortCutTypes = [{id: 'today', name: $filter('translate')('modals:loads_log:search:fields:type:today')},
                          {id: 'yesterday', name: $filter('translate')('modals:loads_log:search:fields:type:yesterday')},
                          {id: 'sevenday', name: $filter('translate')('modals:loads_log:search:fields:type:sevenday')},
                          {id: 'thismonth', name: $filter('translate')('modals:loads_log:search:fields:type:thismonth')},
                          {id: 'lastsixmonths', name: $filter('translate')('modals:loads_log:search:fields:type:lastsixmonths')},
                          {id: 'lastyear', name: $filter('translate')('modals:loads_log:search:fields:type:lastyear')},
                          {id: 'custom', name: $filter('translate')('modals:loads_log:search:fields:type:custom')}];


    /* Validate and send form data to API */
    vm.sendDataLoadsLog = function(searchLoadLogFormName) {
      // Validate form.
      if (searchLoadLogFormName.$invalid) {
          // If there was an error for required fields.
          if (searchLoadLogFormName.$error.required) {
              toastr.error($filter('translate')('modals:loads_log:search:form:required:fields:error'));
          } else {
              toastr.error($filter('translate')('modals:loads_log:search:form:required:fields:invalid'));
          }
          return;
      }

      if(vm.searchData.shortcut == 'custom' && !vm.searchData.load_id){
        if(!vm.searchData.date_from || !vm.searchData.date_to || angular.isDefined(vm.searchData.date_from.startDate) || angular.isDefined(vm.searchData.date_to.startDate)){
          toastr.error($filter('translate')('modals:loads_log:search:form:required:fields:error'));
          return;
        }
      }

      vm.closePopup(vm.searchData);
    };

    /* Close popup */
    vm.closePopup = function(resp) {
        $uibModalInstance.close(resp);
    };

    /* Validate if date_to is greater than date_from */
    vm.allowDateTo = function() {
      if(vm.searchData.date_from != null && vm.searchData.date_from != "" && !angular.isDefined(vm.searchData.date_from.startDate)){
        vm.dateToDisabled = false;
        if(vm.searchData.date_to < vm.searchData.date_from){
          vm.searchData.date_to = vm.searchData.date_from;
        }
      }else{
        vm.dateToDisabled = true;
        vm.searchData.date_to = null;
      }
    };
  }
})();

(function() {
  'use strict';

  /* Report issues load modal controller
   * Receive load id 
   * Send form data to API
   */

  angular
    .module('loadsAppWeb')
    .controller('ReportIssuesLoadModalController', ReportIssuesLoadModalController);

  /** @ngInject */
  function ReportIssuesLoadModalController($state, load, Auth, Loads, $uibModalInstance, DatatableTool, $q, toastr, $filter) {
	  var vm = this;

    /* Function executed at strart, set form vars  */
    vm.initialize = function() {
      vm.loadId = load;
      vm.dataToSend = {};
      vm.dataToSend.token = Auth.getUserToken();
      vm.dataToSend.load_id = vm.loadId;
      vm.dataToSend.description = null;
      vm.savingReport = false;
    };

    /* Validate and send form data to API */
    vm.sendReport = function(reportIssuesLoadFormName) {
      vm.savingReport = true;
      // Validate form.
      if (reportIssuesLoadFormName.$invalid) {
          // If there was an error for required fields.
          if (reportIssuesLoadFormName.$error.required) {
              toastr.error($filter('translate')('modals:report_issues_load:form:required:fields:error'));
          } else {
              toastr.error($filter('translate')('modals:report_issues_load:form:required:fields:invalid'));
          }
          vm.savingReport = false;
          return;
      }

      var defer = $q.defer();

      Loads.reportIssueToLoad(vm.dataToSend)
          .then(function (response) {
                  toastr.success($filter('translate')('modals:report_issues_load:form:success'));
                  vm.closePopup();
              }, function (error) {
                  toastr.error($filter('translate')('modals:report_issues_load:form:required:fields:invalid'));
              });

      vm.savingReport = false;
    };

    /* Close popup */
    vm.closePopup = function() {
        $uibModalInstance.close();
    };

    vm.initialize();
    
  }
})();

(function() {
  'use strict';

  /* Loads log list controller
   * Set filter data
   * Look for loads log list about filters
   * Show load details in dropdown
   */

  angular
    .module('loadsAppWeb')
    .controller('LoadsLogController', LoadsLogController);

  /** @ngInject */
  function LoadsLogController(toastr, $filter, Config, Loads, Auth, DatatableTool, DTOptionsBuilder, DTColumnBuilder, $sce, $uibModal) {
	  var vm = this;

    /* Function executed at strart, Set first params and look for loads  */
	  vm.initialize = function() {
      vm.profile = Auth.getUserProfile();

      vm.datePicker = {};
      vm.datePicker.date = {startDate: null, endDate: null};
      vm.loadsOptions = {
        'token': Auth.getUserToken(),
        'page': 1
      };
      vm.loadsList = [];
      vm.loadingLoadData = true;
      vm.initializedList = false;

      vm.countLoads = 0;

      vm.loadTab();

      //vm.getLoadsLogData();  
    };

    /* Load table first time or reload it  */
    vm.loadTab = function() {
      if (vm.initializedList) {
        vm.reloadList();
      } else {
        vm.initializeList();
      }
    };

    /* Get loads list and set datatable  */
    vm.initializeList = function() {
      vm.datatableOptions = DTOptionsBuilder.newOptions()
                            .withOption('ajax', function(data, callback, settings){
                                vm.countLoads = 0;
                                // Page
                                vm.loadsOptions.page = DatatableTool.getPage(data);

                                Loads.getLoadsHistoryByUserRol(vm.loadsOptions)
                                  .then(function(response){
                                    var records = DatatableTool.getRecords(response);

                                    if (!angular.isUndefined(records.recordsTotal)) {
                                      vm.countLoads = records.recordsTotal;
                                    }

                                    // Info
                                    if (!angular.isUndefined(records.data)) {
                                        _.forEach(records.data, function(value, key) {

                                            if(vm.profile == 'CARRIER' || vm.profile == 'DRIVER' || vm.profile == 'OWNER'){
                                              value.full_name_shipper = value.loadGenerator.company_name;
                                              value.img_shipper = false;
                                              if(angular.isDefined(value.loadGenerator.user.img_perfil_arr) && value.loadGenerator.user.img_perfil_arr != null){
                                                value.img_shipper = value.loadGenerator.user.img_perfil_arr[0]['imagen-60'];
                                              }
                                            }

                                            value.status_btn = '';
                                            var bgColorBtn = ''
                                            , colorBtn = '';

                                            switch(value.status_loads){
                                              case 'NOT_ASSIGNED':
                                                bgColorBtn = 'bg-color-7';
                                                colorBtn = 'color-armadillo';
                                                break;
                                              case 'ACEPTADA':
                                                bgColorBtn = 'bg-color-2';
                                                colorBtn = 'color-armadillo';
                                                break;
                                              case 'EN_TRANSITO':
                                                bgColorBtn = 'bg-color-5';
                                                colorBtn = 'color-armadillo';
                                                break;
                                              case 'ENTREGADA': 
                                                bgColorBtn = 'bg-color-3';
                                                colorBtn = 'color-armadillo';
                                                break;
                                              case 'CANCELADA': 
                                                bgColorBtn = 'bg-color-4';
                                                colorBtn = 'color-armadillo';
                                                break;
                                              case 'PAGADA': 
                                                bgColorBtn = 'bg-color-8';
                                                colorBtn = 'color-white';
                                                break;
                                              default:
                                                bgColorBtn = 'bg-color-7';
                                                colorBtn = 'color-armadillo';
                                                break;
                                            }


                                            value.status_btn = $sce.trustAsHtml('<button class="btn btn-class2 btn-status-load '+bgColorBtn+' '+colorBtn+'">'+$filter('translate')('loads:table:status:' +value.status_loads.toLowerCase()+ ':text')+'</button>');

                                            value.pickup_date_formatted = moment(value.pickup_date, "YYYY-MM-DD HH:mm:ss").format("MMM DD YYYY");
                                            value.delivery_date_formatted = moment(value.delivery_date, "YYYY-MM-DD HH:mm:ss").format("MMM DD YYYY");

                                            value.price_to_show = "$"+value.price;

                                            if(value.posteverywhere == "1" && value.price == 0){
                                              value.price_to_show = $filter('translate')('loads:table:price:call:text');
                                            }

                                            value.actions = '<i ng-click="loadslog.showDetailsUnique('+value.load_id+')" class="fa fa-angle-down show-load-details-btn"></i>';
                                        });
                                    }
                                    vm.loadsList = records.data;
                                    records.data = [{img_shipper: false, full_name_shipper: '', status_btn: '', from: '', to: '', pickup_date_formatted: '', delivery_date_formatted: '', distance: '', length_loads: '', weight: '', dimension: '', price_to_show: '', load_date: '', actions: ''}];
                                    callback(records);
                                  })
                                  .catch(function (error) {
                                    vm.loadsList = [];
                                    callback(DatatableTool.getRecords(null));
                                  });
                            })
                            .withDataProp('data')
                            .withOption('processing', true)
                            .withOption('serverSide', true)
                            .withOption('searching', false)
                            .withOption('info', false)
                            .withOption('lengthChange', false)
                            .withOption('pageLength', DatatableTool.getLimit(null))
                            .withOption('responsive', true)
                            .withLanguage(DatatableTool.getDefaultLanguageSettings())
                            .withPaginationType('numbers');
      switch(vm.profile){
        case 'GENERADOR_CARGA':
          vm.dtColumns = [
            DTColumnBuilder.newColumn('status_btn'),
            DTColumnBuilder.newColumn('from'),
            DTColumnBuilder.newColumn('to'),
            DTColumnBuilder.newColumn('pickup_date_formatted'),
            DTColumnBuilder.newColumn('delivery_date_formatted'),
            DTColumnBuilder.newColumn('distance'),
            DTColumnBuilder.newColumn('length_loads'),
            DTColumnBuilder.newColumn('weight'),
            DTColumnBuilder.newColumn('dimension'),
            DTColumnBuilder.newColumn('price_to_show'),
            DTColumnBuilder.newColumn('load_date'),
            DTColumnBuilder.newColumn('actions')
          ];
          break;
        case 'CARRIER':
        case 'DRIVER':
        case 'OWNER':
          vm.dtColumns = [
            DTColumnBuilder.newColumn('img_shipper'),
            DTColumnBuilder.newColumn('full_name_shipper'),
            DTColumnBuilder.newColumn('from'),
            DTColumnBuilder.newColumn('to'),
            DTColumnBuilder.newColumn('pickup_date_formatted'),
            DTColumnBuilder.newColumn('delivery_date_formatted'),
            DTColumnBuilder.newColumn('length_loads'),
            DTColumnBuilder.newColumn('weight'),
            DTColumnBuilder.newColumn('dimension'),
            DTColumnBuilder.newColumn('price_to_show'),
            DTColumnBuilder.newColumn('load_date'),
            DTColumnBuilder.newColumn('status_btn'),
            DTColumnBuilder.newColumn('actions')
          ];
          break;
      }

      vm.loadingLoadData = false;
      vm.initializedList = true;
    };

    /* Reload data table  */
    vm.reloadList = function() {
        // Reload datatables
        DatatableTool.reload();
    };

    vm.queryLoads = function(params) {
      Loads.getLoadsHistoryByUserRol(params)
        .then(function(response){
          return response;
        }, function(error){
          return false;
        });
    };

    /* Get loads log list  */
    vm.getLoadsLogData = function() {
      vm.loadsList = [];
      
      Loads.getLoadsHistoryByUserRol(vm.loadsOptions)
        .then(function(response){
          console.log(response);
          if(angular.isDefined(response.results)){
            vm.loadsList = response.results;
          }else{
            vm.loadsList = [];
          }

          vm.loadingLoadData = false;
        }, function(error){
          vm.loadsList = [];
          vm.loadingLoadData = false;
        });
    };

    /* Show Load details in drop down tab */
    vm.showDetailsUnique = function(load_id) {
      for(var i in vm.loadsList){
        if(vm.loadsList[i].load_id == load_id){
          vm.loadsList[i].expanded = !vm.loadsList[i].expanded;
        }else{
          vm.loadsList[i].expanded = false;
        }
      }
    };

    /* Open searching params modal */
    vm.openSearchLoadsLogsModal = function() {
      var modalInstance = $uibModal.open({
        controller: 'SearchLoadLogModalController as searchloadlog',
        windowClass: 'search-load-log-modal',
        templateUrl: 'app/loads/templates/modal/search-load-log.html'
      });

      modalInstance.result.then(function(data) {
        if(data.shortcut){
          vm.loadsOptions.shortcut = data.shortcut;
          if(data.shortcut == 'custom'){
            vm.loadsOptions.date_from = moment(data.date_from).format('YYYY-MM-DD');
            vm.loadsOptions.date_to = moment(data.date_to).format('YYYY-MM-DD');
          }else{
            if(angular.isDefined(vm.loadsOptions.date_from)){
              delete vm.loadsOptions.date_from;
            }

            if(angular.isDefined(vm.loadsOptions.date_to)){
              delete vm.loadsOptions.date_to;
            }
          }
        }else{
          if(angular.isDefined(vm.loadsOptions.shortcut)){
            delete vm.loadsOptions.shortcut;
          }

          if(angular.isDefined(vm.loadsOptions.date_from)){
            delete vm.loadsOptions.date_from;
          }

          if(angular.isDefined(vm.loadsOptions.date_to)){
            delete vm.loadsOptions.date_to;
          }
        }

        if(data.load_id){
          vm.loadsOptions.load_id = data.load_id;
        }else{
          if(angular.isDefined(vm.loadsOptions.load_id)){
            delete vm.loadsOptions.load_id;
          }
        }

        vm.reloadList();
      });
    };

    vm.initialize();
  }
})();

(function() {
  'use strict';

  /* Load edition controller
   * Set form vars
   * Get load attributes
   * Get load info
   * Validate and submit form data
   * Upload images and documents
   */

  angular
    .module('loadsAppWeb')
    .controller('LoadsEditController', LoadsEditController);

  /** @ngInject */
  function LoadsEditController($state, $stateParams, toastr, $filter, Config, Loads, Auth, DatatableTool, $q, $uibModal) {
	  var vm = this;

    /* Function executed at strart, set form vars and get load attributes  */
	  vm.initialize = function() {
      vm.reviewPriceWs = true;
      vm.priceSuggested = 0;
      vm.validStatuses = ['NOT_ASSIGNED'];
      vm.loadId = $stateParams.id|| false;
      vm.loadData = false;
      vm.dataSelectPickupCity = null;
      vm.dataSelectDeliveryCity = null;
      vm.dataSelectPickupAddress = null;
      vm.dataSelectDeliveryAddress = null;

      vm.dateDeliveryDisabled = true;
      vm.datePickerMinDate = moment();
      vm.optsDatePicker = {
          singleDatePicker: true,
          showDropdowns: true,
          locale: {
              format: "YYYY-MM-DD",
              applyClass: 'btn-green',
              applyLabel: $filter('translate')('daterangepicker:btn:success:text'),
              fromLabel: $filter('translate')('daterangepicker:label:from'),
              toLabel: $filter('translate')('daterangepicker:label:to'),
              cancelLabel: $filter('translate')('daterangepicker:btn:cancel:text'),
              customRangeLabel: $filter('translate')('daterangepicker:title:text'),
              firstDay: 1
          }
        };

      vm.dataUpdateLoad = {};
      vm.dataUpdateLoad.token = Auth.getUserToken();
      vm.dataUpdateLoad.load_id = null;
      vm.dataUpdateLoad.city_pickup = null;
      vm.dataUpdateLoad.pickup_date = null;
      vm.dataUpdateLoad.pickup_address = null;
      vm.dataUpdateLoad.detail_address_pickup = null;
      vm.dataUpdateLoad.lat_pickup_address = null;
      vm.dataUpdateLoad.long_pickup_address = null;
      vm.dataUpdateLoad.zip_code_pickup = null,
      vm.dataUpdateLoad.city_delivery = null;
      vm.dataUpdateLoad.delivery_date = null;
      vm.dataUpdateLoad.delivery_address = null;
      vm.dataUpdateLoad.detail_address_delivery = null;
      vm.dataUpdateLoad.lat_delivery_address = null;
      vm.dataUpdateLoad.long_delivery_address = null;
      vm.dataUpdateLoad.zip_code_delivery = null;
      vm.dataUpdateLoad.weight = null;
      vm.dataUpdateLoad.load_type = null;
      vm.dataUpdateLoad.lenght = null;
      vm.dataUpdateLoad.dimension = null;
      vm.dataUpdateLoad.price = null;
      vm.dataUpdateLoad.frecuency_active = 0;
      vm.dataUpdateLoad.load_comments = null;

      vm.loadFilesList = [];

      vm.savingUpdateLoad = false;
      vm.loadingDataDetails = true;

      vm.loadTypeFieldRequired = false;
      vm.carrierSkillsRequired = false;

      vm.optionsGoogleInputCity = {
        types: ['(cities)']
      }

      vm.getItemsSizeLengthLoads();
    };

    /* Get load attributes */
    vm.getItemsSizeLengthLoads = function() {
      vm.itemsSizeLenLoads = {};
      Loads.getItemsSizeLengthLoads()
        .then(function(response){
          if(response){
            vm.itemsSizeLenLoads = response;
            if(angular.isDefined(vm.itemsSizeLenLoads.LoadsSkillsRequired_list)){
              vm.setLoadsSkillsRequiredList();
            }

            if(angular.isDefined(vm.itemsSizeLenLoads.LoadsType_list)){
              vm.setLoadsTypeList();
            }
            vm.getDataLoad();
          }else{
            $state.go('private.loads');
          }
        })
        .catch(function (error) {
          console.log(error);
          $state.go('private.loads');
        }); 
    };

    /* Set load skills list as local var */
    vm.setLoadsSkillsRequiredList = function() {
      vm.skillsRequiredCarrier = [];
      if(vm.itemsSizeLenLoads.LoadsSkillsRequired_list.length){
        for(var i in vm.itemsSizeLenLoads.LoadsSkillsRequired_list){
          vm.skillsRequiredCarrier.push({id: vm.itemsSizeLenLoads.LoadsSkillsRequired_list[i].id, name: vm.itemsSizeLenLoads.LoadsSkillsRequired_list[i].skills_required_name, checked: false});
        }
      }
    };

    /* Set load types list as local var */
    vm.setLoadsTypeList = function() {
      vm.loadTypeList = [];
      if(vm.itemsSizeLenLoads.LoadsType_list.length){
        for(var i in vm.itemsSizeLenLoads.LoadsType_list){
          vm.loadTypeList.push({id: vm.itemsSizeLenLoads.LoadsType_list[i].id, name: vm.itemsSizeLenLoads.LoadsType_list[i].skill_name, checked: false});
        }
      }
    };

    /* Get load info  */
    vm.getDataLoad = function() {
      var loadOptions = {
        'token': Auth.getUserToken(),
        'load_id': (vm.loadId) ? vm.loadId : false
      };

      if(loadOptions.load_id){
        Loads.getLoadDetails(loadOptions)
          .then(function(response){
            if(response.result){
              if(vm.validStatuses.indexOf(response.result.status_loads) != -1){
                vm.loadData = response.result;
                if(angular.isDefined(vm.loadData.load_documents) && vm.loadData.load_documents.length){
                  var arr_img = '';
                  var elements_img = '';
                  for(var f_index in vm.loadData.load_documents){
                    arr_img = $.parseJSON('['+vm.loadData.load_documents[f_index].invoice_file+']');
                    elements_img = arr_img[0][0].normal.split("/");
                    vm.loadFilesList.push({id: f_index + 1,
                                           url: arr_img[0][0].normal, 
                                           title: elements_img[elements_img.length - 1]
                                         });
                  }
                }
                vm.setDataToForm();
                vm.loadingDataDetails = false;
              }else{
                $state.go('private.loads');  
              }
            }else{
              $state.go('private.loads');
            }
          })
          .catch(function (error) {
            vm.loadingDataDetails = false;
            $state.go('private.loads');
          });
      }else{
        vm.loadingDataDetails = false;
        $state.go('private.loads');
      }
    };

    /* Set load info in local vars  */
    vm.setDataToForm = function() {
      vm.dataUpdateLoad.load_id = (angular.isDefined(vm.loadData.load_id)) ? vm.loadData.load_id : null;
      vm.dataUpdateLoad.city_pickup = (angular.isDefined(vm.loadData.from)) ? vm.loadData.from : null;
      vm.dataUpdateLoad.pickup_date = (angular.isDefined(vm.loadData.pickup_date)) ? moment(vm.loadData.pickup_date) : null;
      vm.dataUpdateLoad.pickup_address = (angular.isDefined(vm.loadData.pickup_address)) ? vm.loadData.pickup_address : null;
      vm.dataUpdateLoad.detail_address_pickup = (angular.isDefined(vm.loadData.detail_address_pickup)) ? vm.loadData.detail_address_pickup : null;
      vm.dataUpdateLoad.lat_pickup_address = (angular.isDefined(vm.loadData.lat_pickup_adress)) ? vm.loadData.lat_pickup_adress : null;
      vm.dataUpdateLoad.long_pickup_address = (angular.isDefined(vm.loadData.long_pickup_adress)) ? vm.loadData.long_pickup_adress : null;
      vm.dataUpdateLoad.zip_code_pickup = (angular.isDefined(vm.loadData.zip_code_pickup)) ? vm.loadData.zip_code_pickup : null;
      vm.dataUpdateLoad.city_delivery = (angular.isDefined(vm.loadData.to)) ? vm.loadData.to : null;
      vm.dataUpdateLoad.delivery_date = (angular.isDefined(vm.loadData.delivery_date)) ? moment(vm.loadData.delivery_date) : null;
      vm.dataUpdateLoad.delivery_address = (angular.isDefined(vm.loadData.delivery_address)) ? vm.loadData.delivery_address : null;
      vm.dataUpdateLoad.detail_address_delivery = (angular.isDefined(vm.loadData.detail_address_delivery)) ? vm.loadData.detail_address_delivery : null;
      vm.dataUpdateLoad.lat_delivery_address = (angular.isDefined(vm.loadData.lat_delivery_address)) ? vm.loadData.lat_delivery_address : null;
      vm.dataUpdateLoad.long_delivery_address = (angular.isDefined(vm.loadData.long_delivery_address)) ? vm.loadData.long_delivery_address : null;
      vm.dataUpdateLoad.zip_code_delivery = (angular.isDefined(vm.loadData.zip_code_delivery)) ? vm.loadData.zip_code_delivery : null;
      vm.dataUpdateLoad.weight = (angular.isDefined(vm.loadData.weight)) ? parseInt(vm.loadData.weight) : null;
      if(vm.loadData.type_load && vm.loadData.type_load.length > 0){
        vm.dataUpdateLoad.load_type = [];
        for(var index in vm.loadData.type_load){
          vm.dataUpdateLoad.load_type.push(vm.loadData.type_load[index].id.toString());
        }

        if(angular.isDefined(vm.loadTypeList) && vm.loadTypeList.length){
          vm.loadTypeList.forEach(function(item) {
            if(vm.dataUpdateLoad.load_type.indexOf(item.id.toString()) != -1){
              item.checked = true;
            }
          });
        }
      }
      vm.dataUpdateLoad.lenght = (angular.isDefined(vm.loadData.length_loads)) ? parseInt(vm.loadData.length_loads) : null;
      if(vm.loadData.dimension && vm.itemsSizeLenLoads.LoadsSize_list.length > 0){
        for(var sizeindex in vm.itemsSizeLenLoads.LoadsSize_list){
          if(vm.itemsSizeLenLoads.LoadsSize_list[sizeindex].name == vm.loadData.dimension){
            vm.dataUpdateLoad.dimension = vm.itemsSizeLenLoads.LoadsSize_list[sizeindex].id.toString();

          }
        }
      }
      vm.dataUpdateLoad.price = (angular.isDefined(vm.loadData.price)) ? vm.loadData.price : null;
      vm.dataUpdateLoad.frecuency_active = (angular.isDefined(vm.loadData.recurrent_load_status)) ? (vm.loadData.recurrent_load_status == 'True' ? 1 : 0) : 0;
      if(angular.isDefined(vm.loadData.recurrent)){
        if(angular.isDefined(vm.loadData.recurrent.id)){
          vm.dataUpdateLoad.frecuency_id = vm.loadData.recurrent.id;
        }
      }

      if(vm.loadData.skills_required_carrier && vm.loadData.skills_required_carrier.length > 0){
        vm.dataUpdateLoad.skills_required_carrier = [];
        for(var carrierskillindex in vm.loadData.skills_required_carrier){
          vm.dataUpdateLoad.skills_required_carrier.push(vm.loadData.skills_required_carrier[carrierskillindex].id.toString());
        }

        if(angular.isDefined(vm.skillsRequiredCarrier) && vm.skillsRequiredCarrier.length){
          vm.skillsRequiredCarrier.forEach(function(item) {
            if(vm.dataUpdateLoad.skills_required_carrier.indexOf(item.id.toString()) != -1){
              item.checked = true;
            }
          });
        }
      }
      vm.dataUpdateLoad.load_comments = (angular.isDefined(vm.loadData.comments)) ? vm.loadData.comments : null;

      vm.dataSelectPickupCity = (angular.isDefined(vm.loadData.from)) ? vm.loadData.from : null;
      vm.dataSelectDeliveryCity = (angular.isDefined(vm.loadData.to)) ? vm.loadData.to : null;
      vm.dataSelectPickupAddress = (angular.isDefined(vm.loadData.pickup_address)) ? vm.loadData.pickup_address : null;
      vm.dataSelectDeliveryAddress = (angular.isDefined(vm.loadData.delivery_address)) ? vm.loadData.delivery_address : null;
    };

    /* Validate and send form data to API */
    vm.save = function(postLoadFormName) {
      vm.loadTypeFieldRequired = false;
      vm.carrierSkillsRequired = false;
      vm.savingUpdateLoad = true;
      // Validate form.
      if (postLoadFormName.$invalid) {
          if(angular.isDefined(vm.loadTypeList)){
            var loadTypeChecked = 0;
            vm.loadTypeList.forEach(function(item) {
              if (item.checked) loadTypeChecked++;
            });

            if(loadTypeChecked <= 0){
              vm.loadTypeFieldRequired = true;
            }
          }

          if(angular.isDefined(vm.skillsRequiredCarrier)){
            var carrierSkillsChecked = 0;
            vm.skillsRequiredCarrier.forEach(function(item) {
              if (item.checked) carrierSkillsChecked++;
            });

            if(carrierSkillsChecked <= 0){
              vm.carrierSkillsRequired = true;
            }
          }

          // If there was an error for required fields.
          if (postLoadFormName.$error.required) {
              toastr.error($filter('translate')('loads:add:form:required:fields:error'));
          } else {
              toastr.error($filter('translate')('loads:add:form:required:fields:invalid'));
          }
          vm.savingUpdateLoad = false;
          return;
      }

      if(vm.dataUpdateLoad.lenght <= 0 || vm.dataUpdateLoad.lenght === null || vm.dataUpdateLoad.lenght === null){
        toastr.error($filter('translate')('loads:add:form:length:value:invalid'));
        vm.savingUpdateLoad = false;
        return;
      }

      if(vm.dataUpdateLoad.weight <= 0 || vm.dataUpdateLoad.weight === null || vm.dataUpdateLoad.weight === null){
        toastr.error($filter('translate')('loads:add:form:weight:value:invalid'));
        vm.savingUpdateLoad = false;
        return;
      }

      /*if(vm.dataUpdateLoad.load_type.length){
        vm.dataUpdateLoad.load_type = JSON.stringify(vm.dataUpdateLoad.load_type);
      }*/

      if(angular.isDefined(vm.loadTypeList)){
        vm.dataUpdateLoad.load_type = [];
        vm.loadTypeList.forEach(function(item) {
          if (item.checked) vm.dataUpdateLoad.load_type.push(item.id);
        });

        if(vm.dataUpdateLoad.load_type.length <= 0){
          toastr.error($filter('translate')('loads:add:form:required:fields:error'));
          vm.loadTypeFieldRequired = true;
          vm.savingUpdateLoad = false;
          return;
        }

        vm.dataUpdateLoad.load_type = JSON.stringify(vm.dataUpdateLoad.load_type);
      }

      if(angular.isDefined(vm.skillsRequiredCarrier)){
        vm.dataUpdateLoad.skills_required_carrier = [];
        vm.skillsRequiredCarrier.forEach(function(item) {
          if (item.checked) vm.dataUpdateLoad.skills_required_carrier.push(item.id);
        });

        if(vm.dataUpdateLoad.skills_required_carrier.length <= 0){
          toastr.error($filter('translate')('loads:add:form:required:fields:error'));
          vm.carrierSkillsRequired = true;
          vm.savingUpdateLoad = false;
          return;
        }

        vm.dataUpdateLoad.skills_required_carrier = JSON.stringify(vm.dataUpdateLoad.skills_required_carrier);
      }

      if(typeof vm.dataSelectPickupCity != typeof vm.dataUpdateLoad.city_pickup){
        vm.dataUpdateLoad.city_pickup = (angular.isDefined(vm.dataSelectPickupCity.name) ? vm.dataSelectPickupCity.name : vm.dataSelectPickupCity.formatted_address);  
      }

      if(typeof vm.dataSelectPickupAddress != typeof vm.dataUpdateLoad.pickup_address){
        vm.dataUpdateLoad.pickup_address = vm.dataSelectPickupAddress.formatted_address;
        vm.dataUpdateLoad.lat_pickup_address = (angular.isDefined(vm.dataSelectPickupAddress.geometry) ? vm.dataSelectPickupAddress.geometry.location.lat() : null);
        vm.dataUpdateLoad.long_pickup_address = (angular.isDefined(vm.dataSelectPickupAddress.geometry) ? vm.dataSelectPickupAddress.geometry.location.lng() : null);

        if(angular.isDefined(vm.dataSelectPickupAddress.address_components)){
          for(var i in vm.dataSelectPickupAddress.address_components){
            if(vm.dataSelectPickupAddress.address_components[i].types.indexOf('administrative_area_level_1') != -1){
              vm.dataUpdateLoad.state_pickup = vm.dataSelectPickupAddress.address_components[i].long_name;
            }

            if(vm.dataSelectPickupAddress.address_components[i].types.indexOf('country') != -1){
              vm.dataUpdateLoad.country_pickup = vm.dataSelectPickupAddress.address_components[i].long_name;
            }
          }
        }
      }

      if(typeof vm.dataSelectDeliveryCity != typeof vm.dataUpdateLoad.city_delivery){
        vm.dataUpdateLoad.city_delivery = (angular.isDefined(vm.dataSelectDeliveryCity.name) ? vm.dataSelectDeliveryCity.name : vm.dataSelectDeliveryCity.formatted_address);  
      }

      if(typeof vm.dataSelectDeliveryAddress != typeof vm.dataUpdateLoad.delivery_address){
        vm.dataUpdateLoad.delivery_address = vm.dataSelectDeliveryAddress.formatted_address;
        vm.dataUpdateLoad.lat_delivery_address = (angular.isDefined(vm.dataSelectDeliveryAddress.geometry) ? vm.dataSelectDeliveryAddress.geometry.location.lat() : null);
        vm.dataUpdateLoad.long_delivery_address = (angular.isDefined(vm.dataSelectDeliveryAddress.geometry) ? vm.dataSelectDeliveryAddress.geometry.location.lng() : null);

        if(angular.isDefined(vm.dataSelectDeliveryAddress.address_components)){
          for(var i in vm.dataSelectDeliveryAddress.address_components){
            if(vm.dataSelectDeliveryAddress.address_components[i].types.indexOf('administrative_area_level_1') != -1){
              vm.dataUpdateLoad.state_delivery = vm.dataSelectDeliveryAddress.address_components[i].long_name;
            }

            if(vm.dataSelectDeliveryAddress.address_components[i].types.indexOf('country') != -1){
              vm.dataUpdateLoad.country_delivery = vm.dataSelectDeliveryAddress.address_components[i].long_name;
            }
          }
        }
      }

      if(vm.reviewPriceWs === true){
        var paramsCheckPrice = {};
        paramsCheckPrice.token = vm.dataUpdateLoad.token;
        paramsCheckPrice.origin_city = vm.dataUpdateLoad.city_pickup;
        paramsCheckPrice.origin_state = angular.isDefined(vm.dataUpdateLoad.state_pickup) ? vm.dataUpdateLoad.state_pickup : vm.dataUpdateLoad.city_pickup;
        paramsCheckPrice.destini_city = vm.dataUpdateLoad.city_delivery;
        paramsCheckPrice.destini_state = angular.isDefined(vm.dataUpdateLoad.state_delivery) ? vm.dataUpdateLoad.state_delivery : vm.dataUpdateLoad.city_delivery;
        paramsCheckPrice.type_equipament = vm.dataUpdateLoad.load_type;
        paramsCheckPrice.skills_required_carrier = vm.dataUpdateLoad.skills_required_carrier;

        Loads.checkLoadPriceWs(paramsCheckPrice)
          .then(function (response) {
                  vm.priceSuggested = response.price;
                  vm.openCheckedPriceModal();
              }, function (error) {


              });
      }else{

        var defer = $q.defer();

        Loads.updateLoad(vm.dataUpdateLoad)
            .then(function (response) {
                    console.log(response);
                    toastr.success($filter('translate')('loads:add:form:success'));
                    $state.go('private.loads');
                }, function (error) {
                    toastr.error($filter('translate')('loads:add:form:required:fields:invalid'));
                });

        vm.savingUpdateLoad = false;
      }


    };

    vm.openCheckedPriceModal = function() {
      var modalInstance = $uibModal.open({
        controller: 'CheckedLoadPriceModalController as loadprice',
        windowClass: 'load-price-modal',
        templateUrl: 'app/loads/templates/modal/checked-load-price.html',
        resolve: {
            price: function() {
                return vm.priceSuggested;
            },
            load: function() {
                return vm.dataUpdateLoad;
            }
        }
      });

      modalInstance.result.then(function(price) {
          vm.dataUpdateLoad.price = price;

          Loads.updateLoad(vm.dataUpdateLoad)
            .then(function (response) {
                    console.log(response);
                    toastr.success($filter('translate')('loads:add:form:success'));
                    $state.go('private.loads');
                }, function (error) {
                    toastr.error($filter('translate')('loads:add:form:required:fields:invalid'));
                });

            vm.savingUpdateLoad = false;
      });
    };

    /* Upload documents */
    vm.uploadLegalDoc = function(file, invalidFiles) {
            // Upload logo image.
            console.log(file);
            if (file) {
                // Loading
                vm.fileSaving = true;

                var nameFile = vm.dataUpdateLoad.load_id+'_'+moment().milliseconds();

                if(file.name){
                  var nameFileArr = file.name;
                  nameFileArr = nameFileArr.split(".");
                  nameFile = nameFileArr[0];
                }

                // Upload
                var params = {
                    token: Auth.getUserToken(),
                    invoice_name: nameFile,
                    load_id: vm.dataUpdateLoad.load_id,
                    invoice_type: 'legal_doc',
                    pic: file
                };
                Loads.uploadInvoiceToLoad(params)
                    .then(function(response) {
                        // Success
                        var elements_invoice = '';
                        if(angular.isDefined(response.invoice_name) && response.invoice_name.length > 0){
                          elements_invoice = response.invoice_name[0].normal.split("/");
                          vm.loadFilesList.push({url: response.invoice_name[0].normal, name: elements_invoice[elements_invoice.length - 1]});
                        }
                        toastr.success($filter('translate')('loads:edit:invoice:upload:successfully'));

                        // Loading
                        vm.fileSaving = false;
                    })
                    .catch(function(error) {
                        // Error
                        console.log(error);
                        // Loading
                        vm.fileSaving = false;

                        // Message.
                        toastr.error($filter('translate')('loads:edit:invoice:upload:error'));
                    });
            }
        };

    vm.cleanLoadLengthValue = function() {
      if(vm.dataUpdateLoad.lenght !== undefined && vm.dataUpdateLoad.lenght !== null){
        var textToConvert = vm.dataUpdateLoad.lenght.toString();
        textToConvert = textToConvert.replace(/[a-zA-Z\s\W]/gm, "");
        vm.dataUpdateLoad.lenght = parseInt(textToConvert);
      }else{
        vm.dataUpdateLoad.lenght = 0;
      }
    }
    vm.cleanLoadWeightValue = function() {
      if(vm.dataUpdateLoad.weight !== undefined && vm.dataUpdateLoad.weight !== null){
        var textToConvert = vm.dataUpdateLoad.weight.toString();
        textToConvert = textToConvert.replace(/[a-zA-Z\s\W]/gm, "");
        vm.dataUpdateLoad.weight = parseInt(textToConvert);
      }else{
        vm.dataUpdateLoad.weight = 0;
      }
    }

    vm.initialize();
  }
})();

(function() {
  'use strict';

  /* Load details controller
   * Get load id
   * Get load attributes
   * Get load data
   * Allow user to do actions over the load
   */

  angular
    .module('loadsAppWeb')
    .controller('LoadsDetailsController', LoadsDetailsController);

  /** @ngInject */
  function LoadsDetailsController($state, $stateParams, toastr, $filter, Config, Loads, Auth, DatatableTool, $q, uiGmapGoogleMapApi, uiGmapIsReady, $uibModal) {
	  var vm = this;

    /* Function executed at strart, Get load id, set some vars about loading map, upload images and get load attributes */
	  vm.initialize = function() {
      vm.loadId = $stateParams.id|| false;
      vm.loadData = false;

      vm.loadInvoiceImgFilesList = [];
      vm.loadInvoiceDocsFilesList = [];
      vm.loadLegalImgFilesList = [];
      vm.loadLegalDocsFilesList = [];
      vm.legalDocFileSaving = false;
      vm.dataUpdateLoad = {};

      vm.loadingDataDetails = true;
      vm.loadingMap = true;

      vm.myData = Auth.getUserDataProfile();
      vm.userProfile = Auth.getUserProfile();

      vm.getItemsSizeLengthLoads();
    };

    /* Get load attributes */
    vm.getItemsSizeLengthLoads = function() {
      vm.itemsSizeLenLoads = {};
      Loads.getItemsSizeLengthLoads()
        .then(function(response){
          if(response){
            vm.itemsSizeLenLoads = response;
            if(angular.isDefined(vm.itemsSizeLenLoads.LoadsSkillsRequired_list)){
              vm.setLoadsSkillsRequiredList();
            }

            if(angular.isDefined(vm.itemsSizeLenLoads.LoadsType_list)){
              vm.setLoadsTypeList();
            }
            vm.getDataLoad();
          }else{
            $state.go('private.loads');
          }
        })
        .catch(function (error) {
          console.log(error);
          $state.go('private.loads');
        }); 
    };

    /* Set load skills list as local var */
    vm.setLoadsSkillsRequiredList = function() {
      vm.skillsRequiredCarrier = [];
      if(vm.itemsSizeLenLoads.LoadsSkillsRequired_list.length){
        for(var i in vm.itemsSizeLenLoads.LoadsSkillsRequired_list){
          vm.skillsRequiredCarrier.push({id: vm.itemsSizeLenLoads.LoadsSkillsRequired_list[i].id, name: vm.itemsSizeLenLoads.LoadsSkillsRequired_list[i].skills_required_name, checked: false});
        }
      }
    };

    /* Set load types list as local var */
    vm.setLoadsTypeList = function() {
      vm.loadTypeList = [];
      if(vm.itemsSizeLenLoads.LoadsType_list.length){
        for(var i in vm.itemsSizeLenLoads.LoadsType_list){
          vm.loadTypeList.push({id: vm.itemsSizeLenLoads.LoadsType_list[i].id, name: vm.itemsSizeLenLoads.LoadsType_list[i].skill_name, checked: false});
        }
      }
    };

    /* Get load info */
    vm.getDataLoad = function() {
      var loadOptions = {
        'token': Auth.getUserToken(),
        'load_id': (vm.loadId) ? vm.loadId : false
      };

      if(loadOptions.load_id){
        Loads.getLoadDetails(loadOptions)
          .then(function(response){
            if(response.result){
              vm.loadData = response.result;
              if(angular.isDefined(vm.loadData.load_documents) && vm.loadData.load_documents.length){
                var arr_img = '';
                var elements_img = '';
                for(var f_index in vm.loadData.load_documents){
                  if(vm.loadData.load_documents[f_index].type_file == 'pdf'){
                    elements_img = vm.loadData.load_documents[f_index].invoice_file.split("/");

                    if(vm.loadData.load_documents[f_index].invoice_type == 'legal_doc'){
                      vm.loadLegalDocsFilesList.push({
                        id: f_index + 1,
                        url: vm.loadData.load_documents[f_index].invoice_file, 
                        title: elements_img[elements_img.length - 1],
                        invoice_name: vm.loadData.load_documents[f_index].invoice_name
                      });
                    }else{
                      vm.loadInvoiceDocsFilesList.push({
                        id: f_index + 1,
                        url: vm.loadData.load_documents[f_index].invoice_file, 
                        title: elements_img[elements_img.length - 1],
                        invoice_name: vm.loadData.load_documents[f_index].invoice_name
                      });
                    }
                  }else{
                    arr_img = $.parseJSON('['+vm.loadData.load_documents[f_index].invoice_file+']');
                    elements_img = arr_img[0][0].normal.split("/");

                    if(vm.loadData.load_documents[f_index].invoice_type == 'legal_doc'){
                      vm.loadLegalImgFilesList.push({
                        id: f_index + 1,
                        url: arr_img[0][0].normal, 
                        invoice_name: elements_img[elements_img.length - 1],
                        title: vm.loadData.load_documents[f_index].invoice_name
                      });
                    }else{
                      vm.loadInvoiceImgFilesList.push({
                        id: f_index + 1,
                        url: arr_img[0][0].normal, 
                        invoice_name: elements_img[elements_img.length - 1],
                        title: vm.loadData.load_documents[f_index].invoice_name
                      });
                    }
                  }
                }
              }

              if(angular.isDefined(vm.loadData.type_load) && vm.loadData.type_load.length){
                var arr_typeload = [];
                for(var i in vm.loadData.type_load){
                  arr_typeload.push(vm.loadData.type_load[i].skill_name);
                }
                vm.loadData.type_load_text = arr_typeload.join(', ');
              }else{
                vm.loadData.type_load_text = "";
              }

              if(angular.isDefined(vm.loadData.skills_required_carrier) && vm.loadData.skills_required_carrier.length){
                var arr_carrier_skills_required = [];
                for(var i in vm.loadData.skills_required_carrier){
                  arr_carrier_skills_required.push(vm.loadData.skills_required_carrier[i].skills_required_name);
                }
                vm.loadData.carrier_skills_required_text = arr_carrier_skills_required.join(', ');
              }else{
                vm.loadData.carrier_skills_required_text = "";
              }

              vm.loadData.price_to_show = vm.loadData.price;
              if(vm.loadData.posteverywhere == "1" && vm.loadData.price == 0){
                vm.loadData.price_to_show = $filter('translate')('loads:table:price:call:text');
              }
              
              vm.initializeMap();
              vm.loadingDataDetails = false;
            }else{
              $state.go('private.loads');
            }
          })
          .catch(function (error) {
            vm.loadingDataDetails = false;
            $state.go('private.loads');
          });
      }else{
        vm.loadingDataDetails = false;
        $state.go('private.loads');
      }
    };

    /* Load the map and set load coordinates */
    vm.initializeMap = function(){
      if(angular.isDefined(vm.loadData.from_lat) && vm.loadData.from_lat != "" && angular.isDefined(vm.loadData.from_long) && vm.loadData.from_long != "" && angular.isDefined(vm.loadData.to_lat) && vm.loadData.to_lat != "" && angular.isDefined(vm.loadData.to_long) && vm.loadData.to_long != ""){
        uiGmapGoogleMapApi.then(function(maps) {
         vm.loadingMap = false;
         vm.map = { center: { latitude: vm.loadData.from_lat, longitude: vm.loadData.from_long }, zoom: 14 };
         vm.markers = [
            {id: 111, coords: { latitude: vm.loadData.from_lat, longitude: vm.loadData.from_long}},
            {id: 222, coords: { latitude: vm.loadData.to_lat, longitude: vm.loadData.to_long}}];
        });

        uiGmapIsReady.promise(1).then(function(maps) {
          google.maps.event.trigger(maps, 'resize');
        });
      }
    };

    /* Call Carrier request list modal */
    vm.openCarrierRequestModal = function() {
      var modalInstance = $uibModal.open({
        controller: 'CarrierRequestsModalController as carrierrequests',
        windowClass: 'carrier-requests-modal',
        templateUrl: 'app/loads/templates/modal/carrier-requests.html',
        resolve: {
            load: function() {
                return vm.loadId;
            }
        }
      });

      modalInstance.result.then(function() {
          vm.loadingDataDetails = true;
          vm.initialize();
      });
    };

    /* Call Carrier assigned info modal */
    vm.openCarrierAssignedInfoModal = function() {
      var modalInstance = $uibModal.open({
        controller: 'CarrierAssignedInfoModalController as carrierassignedinfo',
        windowClass: 'carrier-assigned-info-modal',
        templateUrl: 'app/loads/templates/modal/carrier-assigned-info.html',
        resolve: {
            carrier: function() {
                return vm.loadData.carrier;
            }
        }
      });
    };

    /* Call Load activity info modal */
    vm.openLoadActivityInfoModal = function() {
      var modalInstance = $uibModal.open({
        controller: 'LoadActivityInfoModalController as loadactivityinfo',
        windowClass: 'load-activity-info-modal',
        templateUrl: 'app/loads/templates/modal/load-activity-info.html',
        resolve: {
            load: function() {
                return vm.loadData;
            }
        }
      });
    };

    /* Call Truck assigned info modal */
    vm.openTruckAssignedInfoModal = function() {
      var modalInstance = $uibModal.open({
        controller: 'TruckAssignedInfoModalController as truckassignedinfo',
        windowClass: 'truck-assigned-info-modal',
        templateUrl: 'app/loads/templates/modal/truck-assigned-info.html',
        resolve: {
            truck: function() {
                return vm.loadData.info_truck;
            }
        }
      });
    };

    /* Call Truck list to assign modal */
    vm.openChooseTruckModal = function() {
      var modalInstance = $uibModal.open({
        controller: 'LoadTruckSelectModalController as loadtruckselect',
        windowClass: 'load-truck-select-modal',
        templateUrl: 'app/loads/templates/modal/load-truck-select.html',
        resolve: {
            load: function() {
                return vm.loadId;
            },
            carrier: function() {
                return vm.loadData.carrier;
            }
        }
      });

      modalInstance.result.then(function() {
          vm.loadingDataDetails = true;
          vm.initialize();
      });
    };

    /* Call Report issues modal */
    vm.openReportIssuesLoadModal = function() {
      var modalInstance = $uibModal.open({
        controller: 'ReportIssuesLoadModalController as reportissuesload',
        windowClass: 'report-issues-load-modal',
        templateUrl: 'app/loads/templates/modal/report-issues-load.html',
        resolve: {
            load: function() {
                return vm.loadId;
            }
        }
      });
    };

    /* Call Apply to load by carrier modal */
    vm.createRequestCarrierLoad = function() {
      var loadOptions = {
        'token': Auth.getUserToken(),
        'load_id': (vm.loadId) ? vm.loadId : false
      };
      Loads.createRequestCarrierLoad(loadOptions)
        .then(function(response){
          if(angular.isDefined(response.request_created)){
            if(angular.isDefined(response.request_created.idrequest_loads)){
              vm.loadingMap = true;
              vm.loadingDataDetails = true;
              vm.getDataLoad();
            }
          }
        });
    };

    /* Call Cancel my request to apply to the load modal */
    vm.cancelRequestCarrierLoad = function() {
      var loadOptions = {
        'token': Auth.getUserToken(),
        'load_id': (vm.loadId) ? vm.loadId : false
      };
      Loads.cancelRequestCarrierLoad(loadOptions)
        .then(function(response){
          vm.loadingMap = true;
          vm.loadingDataDetails = true;
          vm.getDataLoad();
          vm.legalDocFileSaving = false;
        });
    };

    /* Upload images and documents to the load */
    vm.uploadLegalDoc = function(file, invalidFiles) {
        // Upload logo image.
        if (file) {
            // Loading
            vm.legalDocFileSaving = true;

            var nameFile = vm.loadId+'_'+moment().milliseconds();

            if(file.name){
              var nameFileArr = file.name;
              nameFileArr = nameFileArr.split(".");
              nameFile = nameFileArr[0];
            }

            // Upload
            var params = {
                token: Auth.getUserToken(),
                invoice_name: nameFile,
                load_id: vm.loadId,
                invoice_type: 'legal_doc',
                pic: file
            };
            Loads.uploadInvoiceToLoad(params)
                .then(function(response) {
                    // Success
                    var elements_invoice = '';
                    if(angular.isDefined(response.invoice_name) && response.invoice_name.length > 0){
                      if(response.type_file =='pdf'){
                        elements_invoice = response.invoice_name.split("/");
                        vm.loadLegalDocsFilesList.push({url: response.invoice_name, name: elements_invoice[elements_invoice.length - 1]});
                      }else{
                        elements_invoice = response.invoice_name[0].normal.split("/");
                        vm.loadLegalImgFilesList.push({url: response.invoice_name[0].normal, name: elements_invoice[elements_invoice.length - 1]});
                      }
                    }

                    // Loading
                    vm.legalDocFileSaving = false;
                })
                .catch(function(error) {
                    // Error
                    // Loading
                    vm.legalDocFileSaving = false;
                });
        }
    };

    vm.initialize();
  }
})();

(function() {
  'use strict';

  /* Loads list controller
   * Get filter data
   * Look for loads list about filters
   * Show load details in dropdown
   */

  angular
    .module('loadsAppWeb')
    .controller('LoadsController', LoadsController);

  /** @ngInject */
  function LoadsController($state, $stateParams, toastr, $translate, $filter, Config, Loads, Auth, DatatableTool, DTOptionsBuilder, DTColumnBuilder, $sce) {
	  var vm = this;

    /* Function executed at strart, Get load filters from url params and get loads list  */
	  vm.initialize = function() {
      var search_type = '';
      vm.profile = Auth.getUserProfile();
      vm.doCustomSearch = false;
      vm.countLoads = 0;

      switch(vm.profile){
        case 'GENERADOR_CARGA':
          search_type = 'my_loads';
          break;
        case 'CARRIER':
        case 'DRIVER':
        case 'OWNER':
          if($stateParams.from || $stateParams.to || $stateParams.pickup || $stateParams.delivery || $stateParams.price_min || $stateParams.price_max || $stateParams.length || $stateParams.size || $stateParams.weight || $stateParams.load_type || $stateParams.shipper || $stateParams.review || $stateParams.deadhead || $stateParams.load_id){
            vm.doCustomSearch = true;
            search_type = 'my_search';
            vm.paramsCustomSearch = {};
            vm.paramsCustomSearch.token = Auth.getUserToken();
            vm.paramsCustomSearch.page = 1;
            if($stateParams.from){
              vm.paramsCustomSearch.from = $stateParams.from;
            }
            if($stateParams.to){
              vm.paramsCustomSearch.to = $stateParams.to;
            }
            if($stateParams.pickup){
              vm.paramsCustomSearch.pickup = moment($stateParams.pickup).format('YYYY-MM-DD');
            }
            if($stateParams.delivery){
              vm.paramsCustomSearch.delivery = moment($stateParams.delivery).format('YYYY-MM-DD');
            }
            if($stateParams.price_min){
              vm.paramsCustomSearch.price_min = $stateParams.price_min;
            }
            if($stateParams.price_max){
              vm.paramsCustomSearch.price_max = $stateParams.price_max;
            }
            if($stateParams.length){
              vm.paramsCustomSearch.length = $stateParams.length;
            }
            if($stateParams.size){
              vm.paramsCustomSearch.size = $stateParams.size;
            }
            if($stateParams.weight){
              vm.paramsCustomSearch.weight = $stateParams.weight;
            }
            if($stateParams.load_type){
              vm.paramsCustomSearch.load_type = $stateParams.load_type;
            }
            if($stateParams.load_id){
              vm.paramsCustomSearch.load_id = $stateParams.load_id;
            }
            if($stateParams.shipper){
              vm.paramsCustomSearch.company = $stateParams.shipper;
            }
            if($stateParams.review){
              vm.paramsCustomSearch.review = $stateParams.review;
            }
            if($stateParams.deadhead){
              vm.paramsCustomSearch.deadhead = $stateParams.deadhead;
            }
          }else{
            if(vm.profile == 'CARRIER' || vm.profile ==  'OWNER'){
              search_type = 'all';
            }

            if(vm.profile == 'DRIVER'){
              search_type = 'current';
            }
          }
          break;
      }

      vm.loadsOptions = {
        'search_type': (search_type != '') ? search_type : 'my_loads',
        'token': Auth.getUserToken(),
        'page': 1,
        'id_load': null
      };
      vm.loadsList = [];
      vm.loadingLoadData = true;
      vm.initializedList = false;

      vm.loadTab();  
    };

    /* Load table first time or reload it  */
    vm.loadTab = function() {
      if (vm.initializedList) {
        vm.reloadList();
      } else {
        vm.initializeList();
      }
    };

    /* Change tab filter  */
    vm.changeCategory = function(cat) {
      vm.loadingLoadData = true;
      vm.loadsOptions.search_type = cat;
      vm.loadsOptions.page = 1;

      vm.loadTab();
    };

    /* Get loads list and set datatable  */
    vm.initializeList = function() {
      vm.datatableOptions = DTOptionsBuilder.newOptions()
                            .withOption('ajax', function(data, callback, settings){
                              vm.countLoads = 0;

                              if(vm.loadsOptions.search_type == 'my_search' && vm.doCustomSearch){
                                // Page
                                vm.paramsCustomSearch.page = DatatableTool.getPage(data);

                                Loads.searchLoads(vm.paramsCustomSearch)
                                  .then(function(response){
                                    var records = DatatableTool.getRecords(response);

                                    if (!angular.isUndefined(records.recordsTotal)) {
                                      vm.countLoads = records.recordsTotal;
                                    }

                                    // Info
                                    if (!angular.isUndefined(records.data)) {
                                        _.forEach(records.data, function(value, key) {

                                            if(vm.profile == 'CARRIER' || vm.profile == 'DRIVER' || vm.profile == 'OWNER'){
                                              value.full_name_shipper = value.info_load_generator.company_name;
                                              value.img_shipper = false;
                                              if(angular.isDefined(value.info_load_generator.user.img_perfil_imagenes) && value.info_load_generator.user.img_perfil_imagenes != null){
                                                value.img_shipper = value.info_load_generator.user.img_perfil_imagenes[0]['imagen-60'];
                                              }
                                            }

                                            value.status_btn = '';
                                            var bgColorBtn = ''
                                            , colorBtn = '';

                                            switch(value.status_loads){
                                              case 'NOT_ASSIGNED':
                                                bgColorBtn = 'bg-color-7';
                                                colorBtn = 'color-armadillo';
                                                break;
                                              case 'ACEPTADA':
                                                bgColorBtn = 'bg-color-2';
                                                colorBtn = 'color-armadillo';
                                                break;
                                              case 'EN_TRANSITO':
                                                bgColorBtn = 'bg-color-5';
                                                colorBtn = 'color-armadillo';
                                                break;
                                              case 'ENTREGADA': 
                                                bgColorBtn = 'bg-color-3';
                                                colorBtn = 'color-armadillo';
                                                break;
                                              case 'CANCELADA': 
                                                bgColorBtn = 'bg-color-4';
                                                colorBtn = 'color-armadillo';
                                                break;
                                              case 'PAGADA': 
                                                bgColorBtn = 'bg-color-8';
                                                colorBtn = 'color-white';
                                                break;
                                              default:
                                                bgColorBtn = 'bg-color-7';
                                                colorBtn = 'color-armadillo';
                                                break;
                                            }

                                            value.status_btn = $sce.trustAsHtml('<button class="btn btn-class2 btn-status-load '+bgColorBtn+' '+colorBtn+'">'+$filter('translate')('loads:table:status:' +value.status_loads.toLowerCase()+ ':text')+'</button>');

                                            value.pickup_date_formatted = moment(value.pickup_date, "YYYY-MM-DD HH:mm:ss").format("MMM DD YYYY");
                                            value.delivery_date_formatted = moment(value.delivery_date, "YYYY-MM-DD HH:mm:ss").format("MMM DD YYYY");

                                            value.price_to_show = "$"+value.price;

                                            if(value.posteverywhere == "1" && value.price == 0){
                                              value.price_to_show = $filter('translate')('loads:table:price:call:text');
                                            }

                                            value.actions = '<i ng-click="loads.showDetailsUnique('+value.load_id+')" class="fa fa-angle-down show-load-details-btn"></i>';
                                        });
                                    }
                                    vm.loadsList = records.data;
                                    records.data = [{img_shipper: false, full_name_shipper: '', status_btn: '', from: '', to: '', pickup_date_formatted: '', delivery_date_formatted: '', distance: '', length_loads: '', weight: '', dimension: '', price_to_show: '', load_date: '', actions: ''}];
                                    callback(records);
                                  })
                                  .catch(function (error) {
                                    vm.loadsList = [];
                                    callback(DatatableTool.getRecords(null));
                                  });

                              }else{
                                if(vm.loadsOptions.search_type == 'my_search'){
                                  if(vm.profile == 'CARRIER' || vm.profile == 'OWNER'){
                                    vm.loadsOptions.search_type = 'all';
                                  }

                                  if(vm.profile == 'DRIVER'){
                                    vm.loadsOptions.search_type = 'current';
                                  }
                                }
                                // Page
                                vm.loadsOptions.page = DatatableTool.getPage(data);

                                Loads.getByUserRol(vm.loadsOptions)
                                  .then(function(response){
                                    var records = DatatableTool.getRecords(response);

                                    if (!angular.isUndefined(records.recordsTotal)) {
                                      vm.countLoads = records.recordsTotal;
                                    }

                                    // Info
                                    if (!angular.isUndefined(records.data)) {
                                        _.forEach(records.data, function(value, key) {

                                            if(vm.profile == 'CARRIER' || vm.profile == 'DRIVER' || vm.profile == 'OWNER'){
                                              value.full_name_shipper = value.loadGenerator.company_name;
                                              value.img_shipper = false;
                                              if(angular.isDefined(value.loadGenerator.user.img_perfil_arr) && value.loadGenerator.user.img_perfil_arr != null){
                                                value.img_shipper = value.loadGenerator.user.img_perfil_arr[0]['imagen-60'];
                                              }
                                            }

                                            value.status_btn = '';
                                            var bgColorBtn = ''
                                            , colorBtn = '';

                                            switch(value.status_loads){
                                              case 'NOT_ASSIGNED':
                                                bgColorBtn = 'bg-color-7';
                                                colorBtn = 'color-armadillo';
                                                break;
                                              case 'ACEPTADA':
                                                bgColorBtn = 'bg-color-2';
                                                colorBtn = 'color-armadillo';
                                                break;
                                              case 'EN_TRANSITO':
                                                bgColorBtn = 'bg-color-5';
                                                colorBtn = 'color-armadillo';
                                                break;
                                              case 'ENTREGADA': 
                                                bgColorBtn = 'bg-color-3';
                                                colorBtn = 'color-armadillo';
                                                break;
                                              case 'CANCELADA': 
                                                bgColorBtn = 'bg-color-4';
                                                colorBtn = 'color-armadillo';
                                                break;
                                              case 'PAGADA': 
                                                bgColorBtn = 'bg-color-8';
                                                colorBtn = 'color-white';
                                                break;
                                              default:
                                                bgColorBtn = 'bg-color-7';
                                                colorBtn = 'color-armadillo';
                                                break;
                                            }

                                            value.status_btn = $sce.trustAsHtml('<button class="btn btn-class2 btn-status-load '+bgColorBtn+' '+colorBtn+'">'+$filter('translate')('loads:table:status:' +value.status_loads.toLowerCase()+ ':text')+'</button>');

                                            value.pickup_date_formatted = moment(value.pickup_date, "YYYY-MM-DD HH:mm:ss").format("MMM DD YYYY");
                                            value.delivery_date_formatted = moment(value.delivery_date, "YYYY-MM-DD HH:mm:ss").format("MMM DD YYYY");

                                            value.price_to_show = "$"+value.price;

                                            if(value.posteverywhere == "1" && value.price == 0){
                                              value.price_to_show = $filter('translate')('loads:table:price:call:text');
                                            }

                                            value.actions = '<i ng-click="loads.showDetailsUnique('+value.load_id+')" class="fa fa-angle-down show-load-details-btn"></i>';
                                        });
                                    }
                                    vm.loadsList = records.data;
                                    records.data = [{img_shipper: false, full_name_shipper: '', status_btn: '', from: '', to: '', pickup_date_formatted: '', delivery_date_formatted: '', distance: '', length_loads: '', weight: '', dimension: '', price_to_show: '', load_date: '', actions: ''}];
                                    callback(records);
                                  })
                                  .catch(function (error) {
                                    vm.loadsList = [];
                                    callback(DatatableTool.getRecords(null));
                                  });
                              }
                            })
                            .withDataProp('data')
                            .withOption('processing', true)
                            .withOption('serverSide', true)
                            .withOption('searching', false)
                            .withOption('info', false)
                            .withOption('lengthChange', false)
                            .withOption('pageLength', DatatableTool.getLimit(null))
                            .withOption('responsive', true)
                            .withLanguage(DatatableTool.getDefaultLanguageSettings())
                            .withPaginationType('numbers');
      switch(vm.profile){
        case 'GENERADOR_CARGA':
        //alert($translate.instant('loads:table:column:status'));
          vm.dtColumns = [
            DTColumnBuilder.newColumn('status_btn'),
            DTColumnBuilder.newColumn('from'),
            DTColumnBuilder.newColumn('to'),
            DTColumnBuilder.newColumn('pickup_date_formatted'),
            DTColumnBuilder.newColumn('delivery_date_formatted'),
            DTColumnBuilder.newColumn('distance'),
            DTColumnBuilder.newColumn('length_loads'),
            DTColumnBuilder.newColumn('weight'),
            DTColumnBuilder.newColumn('dimension'),
            DTColumnBuilder.newColumn('price_to_show'),
            DTColumnBuilder.newColumn('load_date'),
            DTColumnBuilder.newColumn('actions')
          ];
          break;
        case 'CARRIER':
        case 'DRIVER':
        case 'OWNER':
          vm.dtColumns = [
            DTColumnBuilder.newColumn('img_shipper'),
            DTColumnBuilder.newColumn('full_name_shipper'),
            DTColumnBuilder.newColumn('from'),
            DTColumnBuilder.newColumn('to'),
            DTColumnBuilder.newColumn('pickup_date_formatted'),
            DTColumnBuilder.newColumn('delivery_date_formatted'),
            DTColumnBuilder.newColumn('length_loads'),
            DTColumnBuilder.newColumn('weight'),
            DTColumnBuilder.newColumn('dimension'),
            DTColumnBuilder.newColumn('price_to_show'),
            DTColumnBuilder.newColumn('load_date'),
            DTColumnBuilder.newColumn('status_btn'),
            DTColumnBuilder.newColumn('actions')
          ];
          break;
      }

      vm.loadingLoadData = false;
      vm.initializedList = true;
    };

    /* Reload data table  */
    vm.reloadList = function() {
        // Reload datatables
        DatatableTool.reload();
    };

    /* Show Load details in drop down tab */
    vm.showDetailsUnique = function(load_id) {
      for(var i in vm.loadsList){
        if(vm.loadsList[i].load_id == load_id){
          vm.loadsList[i].expanded = !vm.loadsList[i].expanded;
        }else{
          vm.loadsList[i].expanded = false;
        }
      }
    };

    vm.initialize();
  }
})();
(function() {
  'use strict';

  /* Load creation controller
   * Set form data
   * Get load attributes
   * Validate and send form data to API
   */

  angular
    .module('loadsAppWeb')
    .controller('LoadsAddController', LoadsAddController);

  /** @ngInject */
  function LoadsAddController($state, toastr, $filter, Config, Loads, Auth, DatatableTool, $q, $uibModal) {
	  var vm = this;

    vm.reviewPriceWs = true;

    /* Function which validates pickup date against delivery date and this doesn't allow pickup date being greater than delivery date */
    vm.allowDeliveryDate = function() {
      if(vm.dataNewLoad.pickup_date != null && vm.dataNewLoad.pickup_date != ""){
        vm.dateDeliveryDisabled = false;
        if(vm.dataNewLoad.delivery_date < vm.dataNewLoad.pickup_date){
          vm.dataNewLoad.delivery_date = vm.dataNewLoad.pickup_date;
        }
      }else{
        vm.dateDeliveryDisabled = true;
        vm.dataNewLoad.delivery_date = null;
      }
    };

    /* Function executed at strart, set form data and get load attributes  */
	  vm.initialize = function() {
      vm.submitReturnList = false;

      vm.priceSuggested = 0;

      vm.dataSelectPickupCity = null;
      vm.dataSelectDeliveryCity = null;
      vm.dataSelectPickupAddress = null;
      vm.dataSelectDeliveryAddress = null;

      vm.dateDeliveryDisabled = true;
      vm.datePickerMinDate = moment();
      vm.optsDatePicker = {
          singleDatePicker: true,
          showDropdowns: true,
          locale: {
              format: "YYYY-MM-DD",
              applyClass: 'btn-green',
              applyLabel: $filter('translate')('daterangepicker:btn:success:text'),
              fromLabel: $filter('translate')('daterangepicker:label:from'),
              toLabel: $filter('translate')('daterangepicker:label:to'),
              cancelLabel: $filter('translate')('daterangepicker:btn:cancel:text'),
              customRangeLabel: $filter('translate')('daterangepicker:title:text'),
              firstDay: 1
          }
        };

      vm.dataNewLoad = {};
      vm.dataNewLoad.token = Auth.getUserToken();
      vm.dataNewLoad.city_pickup = null;
      vm.dataNewLoad.pickup_date = null;
      vm.dataNewLoad.pickup_address = null;
      vm.dataNewLoad.detail_address_pickup = null;
      vm.dataNewLoad.lat_pickup_address = null;
      vm.dataNewLoad.long_pickup_address = null;
      vm.dataNewLoad.country_pickup = null;
      vm.dataNewLoad.state_pickup = null;
      vm.dataNewLoad.zip_code_pickup = null,
      vm.dataNewLoad.city_delivery = null;
      vm.dataNewLoad.delivery_date = null;
      vm.dataNewLoad.delivery_address = null;
      vm.dataNewLoad.detail_address_delivery = null;
      vm.dataNewLoad.lat_delivery_address = null;
      vm.dataNewLoad.long_delivery_address = null;
      vm.dataNewLoad.country_delivery = null;
      vm.dataNewLoad.state_delivery = null;
      vm.dataNewLoad.zip_code_delivery = null;
      vm.dataNewLoad.weight = null;
      vm.dataNewLoad.load_type = [];
      vm.dataNewLoad.lenght = null;
      vm.dataNewLoad.dimension = null;
      vm.dataNewLoad.price = null;
      vm.dataNewLoad.frecuency_active = 0;
      vm.dataNewLoad.skills_required_carrier = [];

      vm.savingNewLoad = false;
      vm.loadTypeFieldRequired = false;
      vm.carrierSkillsRequired = false;

      vm.optionsGoogleInputCity = {
        types: ['(cities)']
      }

      vm.getItemsSizeLengthLoads();

    };

    /* Get load attributes */
    vm.getItemsSizeLengthLoads = function() {
      vm.itemsSizeLenLoads = {};
      Loads.getItemsSizeLengthLoads()
        .then(function(response){
          if(response){
            vm.itemsSizeLenLoads = response;
            if(angular.isDefined(vm.itemsSizeLenLoads.LoadsSkillsRequired_list)){
              vm.setLoadsSkillsRequiredList();
            }

            if(angular.isDefined(vm.itemsSizeLenLoads.LoadsType_list)){
              vm.setLoadsTypeList();
            }
          }
        }); 
    };

    /* Set load skills list as local var */
    vm.setLoadsSkillsRequiredList = function() {
      vm.skillsRequiredCarrier = [];
      if(vm.itemsSizeLenLoads.LoadsSkillsRequired_list.length){
        for(var i in vm.itemsSizeLenLoads.LoadsSkillsRequired_list){
          vm.skillsRequiredCarrier.push({id: vm.itemsSizeLenLoads.LoadsSkillsRequired_list[i].id, name: vm.itemsSizeLenLoads.LoadsSkillsRequired_list[i].skills_required_name, checked: false});
        }
      }
    };

    /* Set load types list as local var */
    vm.setLoadsTypeList = function() {
      vm.loadTypeList = [];
      if(vm.itemsSizeLenLoads.LoadsType_list.length){
        for(var i in vm.itemsSizeLenLoads.LoadsType_list){
          vm.loadTypeList.push({id: vm.itemsSizeLenLoads.LoadsType_list[i].id, name: vm.itemsSizeLenLoads.LoadsType_list[i].skill_name, checked: false});
        }
      }
    };

    /* Validate form and send data to API */
    vm.save = function(postLoadFormName) {
      vm.loadTypeFieldRequired = false;
      vm.carrierSkillsRequired = false;
      vm.savingNewLoad = true;
      // Validate form.
      if (postLoadFormName.$invalid) {
          if(angular.isDefined(vm.loadTypeList)){
            var loadTypeChecked = 0;
            vm.loadTypeList.forEach(function(item) {
              if (item.checked) loadTypeChecked++;
            });

            if(loadTypeChecked <= 0){
              vm.loadTypeFieldRequired = true;
            }
          }

          if(angular.isDefined(vm.skillsRequiredCarrier)){
            var carrierSkillsChecked = 0;
            vm.skillsRequiredCarrier.forEach(function(item) {
              if (item.checked) carrierSkillsChecked++;
            });

            if(carrierSkillsChecked <= 0){
              vm.carrierSkillsRequired = true;
            }
          }
          // If there was an error for required fields.
          if (postLoadFormName.$error.required) {
              toastr.error($filter('translate')('loads:add:form:required:fields:error'));
          } else {
              toastr.error($filter('translate')('loads:add:form:required:fields:invalid'));
          }
          vm.savingNewLoad = false;
          return;
      }

      if(vm.dataNewLoad.lenght <= 0 || vm.dataNewLoad.lenght === null || vm.dataNewLoad.lenght === null){
        toastr.error($filter('translate')('loads:add:form:length:value:invalid'));
        vm.savingNewLoad = false;
        return;
      }

      if(vm.dataNewLoad.weight <= 0 || vm.dataNewLoad.weight === null || vm.dataNewLoad.weight === null){
        toastr.error($filter('translate')('loads:add:form:weight:value:invalid'));
        vm.savingNewLoad = false;
        return;
      }

      /*if(vm.dataNewLoad.load_type.length){
        vm.dataNewLoad.load_type = JSON.stringify(vm.dataNewLoad.load_type);
      }*/

      vm.dataNewLoad.load_type = [];
      if(angular.isDefined(vm.loadTypeList)){
        vm.loadTypeList.forEach(function(item) {
          if (item.checked) vm.dataNewLoad.load_type.push(item.id);
        });

        if(vm.dataNewLoad.load_type.length <= 0){
          toastr.error($filter('translate')('loads:add:form:required:fields:error'));
          vm.loadTypeFieldRequired = true;
          vm.savingNewLoad = false;
          return;
        }

        vm.dataNewLoad.load_type = JSON.stringify(vm.dataNewLoad.load_type);
      }

      vm.dataNewLoad.skills_required_carrier = [];
      if(angular.isDefined(vm.skillsRequiredCarrier)){
        vm.skillsRequiredCarrier.forEach(function(item) {
          if (item.checked) vm.dataNewLoad.skills_required_carrier.push(item.id);
        });

        if(vm.dataNewLoad.skills_required_carrier.length <= 0){
          toastr.error($filter('translate')('loads:add:form:required:fields:error'));
          vm.carrierSkillsRequired = true;
          vm.savingNewLoad = false;
          return;
        }

        vm.dataNewLoad.skills_required_carrier = JSON.stringify(vm.dataNewLoad.skills_required_carrier);
      }

      vm.dataNewLoad.city_pickup = (angular.isDefined(vm.dataSelectPickupCity.name) ? vm.dataSelectPickupCity.name : vm.dataSelectPickupCity.formatted_address);
      vm.dataNewLoad.pickup_address = vm.dataSelectPickupAddress.formatted_address;
      vm.dataNewLoad.lat_pickup_address = (angular.isDefined(vm.dataSelectPickupAddress.geometry) ? vm.dataSelectPickupAddress.geometry.location.lat() : null);
      vm.dataNewLoad.long_pickup_address = (angular.isDefined(vm.dataSelectPickupAddress.geometry) ? vm.dataSelectPickupAddress.geometry.location.lng() : null);
      vm.dataNewLoad.country_pickup = null;
      vm.dataNewLoad.state_pickup = null;

      if(angular.isDefined(vm.dataSelectPickupAddress.address_components)){
        for(var i in vm.dataSelectPickupAddress.address_components){
          if(vm.dataSelectPickupAddress.address_components[i].types.indexOf('administrative_area_level_1') != -1){
            vm.dataNewLoad.state_pickup = vm.dataSelectPickupAddress.address_components[i].long_name;
          }

          if(vm.dataSelectPickupAddress.address_components[i].types.indexOf('country') != -1){
            vm.dataNewLoad.country_pickup = vm.dataSelectPickupAddress.address_components[i].long_name;
          }
        }
      }

      vm.dataNewLoad.city_delivery = (angular.isDefined(vm.dataSelectDeliveryCity.name) ? vm.dataSelectDeliveryCity.name : vm.dataSelectDeliveryCity.formatted_address);
      vm.dataNewLoad.delivery_address = vm.dataSelectDeliveryAddress.formatted_address;
      vm.dataNewLoad.lat_delivery_address = (angular.isDefined(vm.dataSelectDeliveryAddress.geometry) ? vm.dataSelectDeliveryAddress.geometry.location.lat() : null);
      vm.dataNewLoad.long_delivery_address = (angular.isDefined(vm.dataSelectDeliveryAddress.geometry) ? vm.dataSelectDeliveryAddress.geometry.location.lng() : null);
      vm.dataNewLoad.country_delivery = null;
      vm.dataNewLoad.state_delivery = null;

      if(angular.isDefined(vm.dataSelectDeliveryAddress.address_components)){
        for(var i in vm.dataSelectDeliveryAddress.address_components){
          if(vm.dataSelectDeliveryAddress.address_components[i].types.indexOf('administrative_area_level_1') != -1){
            vm.dataNewLoad.state_delivery = vm.dataSelectDeliveryAddress.address_components[i].long_name;
          }

          if(vm.dataSelectDeliveryAddress.address_components[i].types.indexOf('country') != -1){
            vm.dataNewLoad.country_delivery = vm.dataSelectDeliveryAddress.address_components[i].long_name;
          }
        }
      }

      var defer = $q.defer();

      if(vm.reviewPriceWs === true){
        var paramsCheckPrice = {};
        paramsCheckPrice.token = vm.dataNewLoad.token;
        paramsCheckPrice.origin_city = vm.dataNewLoad.city_pickup;
        paramsCheckPrice.origin_state = vm.dataNewLoad.state_pickup;
        paramsCheckPrice.destini_city = vm.dataNewLoad.city_delivery;
        paramsCheckPrice.destini_state = vm.dataNewLoad.state_delivery;
        paramsCheckPrice.type_equipament = vm.dataNewLoad.load_type;
        paramsCheckPrice.skills_required_carrier = vm.dataNewLoad.skills_required_carrier;

        Loads.checkLoadPriceWs(paramsCheckPrice)
          .then(function (response) {
                  vm.priceSuggested = response.price;
                  vm.openCheckedPriceModal();
              }, function (error) {


              });
      }else{
        Loads.saveLoad(vm.dataNewLoad)
        .then(function (response) {
                console.log(response);
                toastr.success($filter('translate')('loads:add:form:success'));
                if(vm.submitReturnList){
                  $state.go('private.loads');
                }else{
                  vm.initialize();
                }
            }, function (error) {
                toastr.error($filter('translate')('loads:add:form:required:fields:invalid'));
            });

        vm.savingNewLoad = false;
      }

    };

    /* Set return load list after save a new load */
    vm.submitReturnListFunc = function(postLoadFormName) {
      vm.submitReturnList = true;
      vm.save(postLoadFormName);
    };

    vm.cleanLoadLengthValue = function() {
      if(vm.dataNewLoad.lenght !== undefined && vm.dataNewLoad.lenght !== null){
        var textToConvert = vm.dataNewLoad.lenght.toString();
        textToConvert = textToConvert.replace(/[a-zA-Z\s\W]/gm, "");
        vm.dataNewLoad.lenght = parseInt(textToConvert);
      }else{
        vm.dataNewLoad.lenght = 0;
      }
    };
    vm.cleanLoadWeightValue = function() {
      if(vm.dataNewLoad.weight !== undefined && vm.dataNewLoad.weight !== null){
        var textToConvert = vm.dataNewLoad.weight.toString();
        textToConvert = textToConvert.replace(/[a-zA-Z\s\W]/gm, "");
        vm.dataNewLoad.weight = parseInt(textToConvert);
      }else{
        vm.dataNewLoad.weight = 0;
      }
    };

    vm.openCheckedPriceModal = function() {
      var modalInstance = $uibModal.open({
        controller: 'CheckedLoadPriceModalController as loadprice',
        windowClass: 'load-price-modal',
        templateUrl: 'app/loads/templates/modal/checked-load-price.html',
        resolve: {
            price: function() {
                return vm.priceSuggested;
            },
            load: function() {
                return vm.dataNewLoad;
            }
        }
      });

      modalInstance.result.then(function(price) {
          vm.dataNewLoad.price = price;

          Loads.saveLoad(vm.dataNewLoad)
            .then(function (response) {
                    console.log(response);
                    toastr.success($filter('translate')('loads:add:form:success'));
                    if(vm.submitReturnList){
                      $state.go('private.loads');
                    }else{
                      vm.initialize();
                    }
                }, function (error) {
                    toastr.error($filter('translate')('loads:add:form:required:fields:invalid'));
                });

            vm.savingNewLoad = false;
      });
    };

    vm.initialize();
  }
})();

(function() {
  'use strict';

  /* Set load truck modal controller
   * Receive load id 
   * Verify load status to assign truck
   * Get trucks list
   * Assign truck to load
   */

  angular
    .module('loadsAppWeb')
    .controller('LoadTruckSelectModalController', LoadTruckSelectModalController);

  /** @ngInject */
  function LoadTruckSelectModalController($state, load, carrier, Auth, Loads, Carriers, Trucks, $uibModalInstance, DatatableTool, $q, toastr, $filter, $sce) {
	  var vm = this;

    /* Function executed at strart, get load id and verify load  */
    vm.initialize = function() {
      vm.datatableOptions = DatatableTool.getDefaultSettings();
      vm.datatableOptions.paging = false;
      vm.loadId = load;
      vm.carrierInfo = carrier;
      vm.loadingInfo = true;
      vm.savingRequest = false;
      vm.truckOptions = {
        'shortcut': 'active',
        'token': Auth.getUserToken(),
        'page': 1
      };

      vm.dataRequest = {};

      vm.dataTruckSelect = {};
      vm.dataTruckSelect.truck_id = null;

      vm.verifyLoad();
    };

    /* Verify load status to allow assigning the truck */
    vm.verifyLoad = function() {
      var params = {
                    'token': Auth.getUserToken(),
                    'load_id': vm.loadId
                   };

      Loads.getLoadDetails(params)
        .then(function(response){
          vm.loadData = response.result;

          if(vm.loadData.status_loads != 'NOT_ASSIGNED' && vm.loadData.status_loads != 'CANCELADA'){ //&& vm.loadData.info_truck == 0
            vm.getTrucks();
          }else{
            vm.closePopup();
          }
        })
        .catch(function (error) {
          vm.closePopup();
        });
    };

    /* Get trucks list by carrier */
    vm.getTrucks = function() {
      Trucks.trucksForCarrier(vm.truckOptions)
        .then(function(response){
          vm.dataRequest = response.truck_results;

          if(vm.dataRequest.length > 0){
            _.forEach(vm.dataRequest, function(value, key) {

                value.truck_img = false;
                if(angular.isDefined(value.img_truck_imagenes) && value.img_truck_imagenes != null){
                  value.truck_img = value.img_truck_imagenes[0]['imagen-60'];
                }

                value.truck_name = value.brand+' '+value.marca+' '+value.model;

                value.driver_name = value.driver.user.user_first_name+' '+value.driver.user.user_last_name;

                value.truck_status = '';
                  var bgColorBtn = ''
                  , colorBtn = '';

                  switch(value.status){
                    case '1':
                      bgColorBtn = 'bg-color-3';
                      colorBtn = 'color-armadillo';
                      break;
                    case '0':
                      bgColorBtn = 'bg-color-7';
                      colorBtn = 'color-armadillo';
                      break;
                  }

                  value.truck_status = $sce.trustAsHtml('<button class="btn btn-class2 '+bgColorBtn+' '+colorBtn+'">'+$filter('translate')('trucks:table:status:' +value.status+ ':text')+'</button>');
            });
          }
          vm.loadingInfo = false;
        })
        .catch(function (error) {
          vm.loadingInfo = false;
        });
    };

    /* Validate truck selected and send data to API */
    vm.sendConfirmSelect = function(loadTruckSelectFormName) {
      vm.savingRequest = true;
      // Validate form.
      if (loadTruckSelectFormName.$invalid) {
          // If there was an error for required fields.
          if (loadTruckSelectFormName.$error.required) {
              toastr.error($filter('translate')('modals:load_truck_select:form:required:fields:error'));
          } else {
              toastr.error($filter('translate')('modals:load_truck_select:form:required:fields:invalid'));
          }
          vm.savingRequest = false;
          return;
      }

      var defer = $q.defer();

      var params = {
                'truck_id': vm.dataTruckSelect.truck_id,
                'load_id': vm.loadId,
                'token': Auth.getUserToken()
              };

      Carriers.assignTruckToLoad(params)
          .then(function (response) {
                  console.log(response);
                  toastr.success($filter('translate')('modals:load_truck_select:form:success'));
                  vm.closePopup();
              }, function (error) {
                  toastr.error($filter('translate')('modals:load_truck_select:form:required:fields:invalid'));
              });

      vm.savingRequest = false;
    };

    /* Close popup */
    vm.closePopup = function() {
        $uibModalInstance.close();
    };

    vm.initialize();
    
  }
})();

