(function() {
  'use strict';

  /* Carrier details controller
   * It gets a Carrier Id and looks for their information
   * Look for user data and related info about load if this page is charged after load page*/

  angular
    .module('loadsAppWeb')
    .controller('CarriersDetailsController', CarriersDetailsController);

  /** @ngInject */
  function CarriersDetailsController($state, $stateParams, toastr, $filter, Config, Carriers, Auth, Loads, DatatableTool, $q) {
	  var vm = this;

    /* Function executed at start, it gets carrier Id and load Id (optional) and executes searching function */
	  vm.initialize = function() {
      vm.carrierId = $stateParams.id|| false;
      vm.loadId = $stateParams.load_id|| false;
      vm.loadCarrierRequestOpts = {
        'token': Auth.getUserToken(),
        'load_id': vm.loadId,
        'page': 1
      };
      vm.loadInfoOpts = {
        'token': Auth.getUserToken(),
        'load_id': vm.loadId
      };
      vm.loadCarrierRequest = false;
      vm.loadData = false;
      vm.shipperCanAcceptRequest = false;
      vm.carrierData = false;

      vm.loadingDataDetails = true;

      vm.myData = Auth.getUserDataProfile();

      vm.getCarrierInfo();
    };

    /* Get info about load */
    vm.getLoadInfo = function() {
      Loads.getLoadDetails(vm.loadInfoOpts)
        .then(function(response){
          vm.loadData = response.result;

          if(angular.isDefined(vm.loadData.info_load_generator)){
            var shipperData = Auth.getUserDataProfile();
            if(vm.loadData.info_load_generator.user_id == shipperData.user_id){
              vm.shipperCanAcceptRequest = true;
            }
          }
        })
        .catch(function (error) {
        });
    };

    /* Get info about carrier request to load */
    vm.getLoadRequestByCarrier = function() {
      Loads.getCarrierRequests(vm.loadCarrierRequestOpts)
        .then(function(response){
          var carrierReqList = response.results;

          if(carrierReqList.length){
            for(var i in carrierReqList){
              if(carrierReqList[i].carrier.user_id == vm.carrierData.carrier_info.user_id){
                vm.loadCarrierRequest = carrierReqList[i];
              }
            }
          }
        })
        .catch(function (error) {
        });
    };

    /* Get info about carrier */
    vm.getCarrierInfo = function() {
      var carrierOptions = {
        'token': Auth.getUserToken(),
        'page': 1,
        'id_carrier': (vm.carrierId) ? vm.carrierId : false
      };

      if(carrierOptions.id_carrier){
        Carriers.getCarrierInformation(carrierOptions)
          .then(function(response){
            if(response){
              vm.carrierData = response;

              if(angular.isDefined(vm.carrierData.carrier_info)){
                if(angular.isDefined(vm.carrierData.carrier_info.user) && angular.isDefined(vm.carrierData.carrier_info.user.img_perfil)){
                  vm.carrierData.img_perfil_carrier = $.parseJSON('['+vm.carrierData.carrier_info.user.img_perfil+']');
                  vm.carrierData.img_perfil_carrier = vm.carrierData.img_perfil_carrier[0];
                }
              }

              if(vm.loadId){
                vm.getLoadRequestByCarrier();
                vm.getLoadInfo();
              }

              vm.loadingDataDetails = false;
            }else{
              vm.loadingDataDetails = false;
              $state.go('private.home');
            }
          })
          .catch(function (error) {
            vm.loadingDataDetails = false;
            $state.go('private.home');
          });
      }else{
        vm.loadingDataDetails = false;
        $state.go('private.home');
      }
    };

    /* Function allows shipper accepting some carrier request of the load */
    vm.answerRequestByCarrier = function() {
      if(vm.loadCarrierRequest && vm.shipperCanAcceptRequest){

        var params = {
                'id_request': vm.loadCarrierRequest.id_request,
                'token': Auth.getUserToken(),
                'load_id': vm.loadId
              }

        Loads.acceptCarrierRequest(params)
            .then(function (response) {
                    toastr.success($filter('translate')('modals:carrier_requests:form:success'));
                    vm.shipperCanAcceptRequest = false;
                }, function (error) {
                    toastr.error($filter('translate')('modals:carrier_requests:form:required:fields:invalid'));
                });

      }
    };

    vm.initialize();
  }
})();

(function() {
  'use strict';

  /* Carrier controller
   * If it receives searching params, it'll prepare filter vars to get carriers 
   * Set and reset filters
   * Look for carriers and set structure table
   * Show carrier details in dropdown section*/

  angular
    .module('loadsAppWeb')
    .controller('CarriersController', CarriersController);

  /** @ngInject */
  function CarriersController($state, $stateParams, toastr, $translate, $filter, Config, Carriers, Auth, DatatableTool, DTOptionsBuilder, DTColumnBuilder, $q, $uibModal) {
    var vm = this;

    /* Set controller vars and set filters if it receive some of them 
     * execute searching function at the end*/
    vm.initialize = function() {
      vm.carriersList = [];
      vm.tabList = "all";
      vm.initializedList = false;
      vm.loadingCarrierData = true;
      //vm.datatableOptions = DatatableTool.getDefaultSettings();
      //vm.datatableOptions.paging = false;
      vm.params = {};
      vm.params.company = $stateParams.company|| false;
      vm.params.trailer_type = $stateParams.trailer_type|| false;
      vm.params.length = $stateParams.len|| false;
      vm.params.weight = $stateParams.wei|| false;
      vm.params.size = $stateParams.size|| false;
      vm.params.skils_truck = $stateParams.skils_truck|| false;
      vm.params.reviews = $stateParams.reviews|| false;

      vm.searchOptions = {};
      vm.searchOptions.page = 1;
      vm.searchOptions.token = Auth.getUserToken();
      if(vm.params.company){
        vm.tabList = "my_search";
        vm.searchOptions.namecarrier = vm.params.company;
      }
      if(vm.params.trailer_type){
        vm.tabList = "my_search";
        vm.searchOptions.type_trailer = vm.params.trailer_type;
      }
      if(vm.params.length){
        vm.tabList = "my_search";
        vm.searchOptions.lenght = vm.params.length;
      }
      if(vm.params.weight){
        vm.tabList = "my_search";
        vm.searchOptions.weight = vm.params.weight;
      }
      if(vm.params.size){
        vm.tabList = "my_search";
        vm.searchOptions.size = vm.params.size;
      }
      if(vm.params.skils_truck){
        vm.tabList = "my_search";
        //vm.searchOptions.skills = JSON.parse("["+vm.params.skils_truck+"]");
        vm.searchOptions.skills = JSON.stringify(vm.params.skils_truck);
      }
      if(vm.params.reviews && vm.params.reviews > 0){
        vm.tabList = "my_search";
        vm.searchOptions.reviews = vm.params.reviews;
      }

      vm.loadTab();

      //vm.searchCarriers();
    };

    /* Set searching params if user select "Searching" tab */
    vm.setSearchParams = function() {
      var deferred = $q.defer();

      vm.searchOptions = {};
      vm.searchOptions.page = 1;
      vm.searchOptions.token = Auth.getUserToken();
      if(vm.params.company){
        vm.tabList = "my_search";
        vm.searchOptions.namecarrier = vm.params.company;
      }
      if(vm.params.trailer_type){
        vm.tabList = "my_search";
        vm.searchOptions.type_trailer = vm.params.trailer_type;
      }
      if(vm.params.length){
        vm.tabList = "my_search";
        vm.searchOptions.lenght = vm.params.length;
      }
      if(vm.params.weight){
        vm.tabList = "my_search";
        vm.searchOptions.weight = vm.params.weight;
      }
      if(vm.params.size){
        vm.tabList = "my_search";
        vm.searchOptions.size = vm.params.size;
      }
      if(vm.params.skils_truck){
        vm.tabList = "my_search";
        vm.searchOptions.skills = JSON.parse("["+vm.params.skils_truck+"]");
      }
      if(vm.params.reviews && vm.params.reviews > 0){
        vm.tabList = "my_search";
        vm.searchOptions.reviews = vm.params.reviews;
      }

      deferred.resolve(true);
      return deferred.promise;
    };

    /* reset all searching filters */
    vm.resetAllFilters = function() {
      vm.tabList = "all";
      vm.params.company = false;
      vm.params.trailer_type = false;
      vm.params.length = false;
      vm.params.weight = false;
      vm.params.size = false;
      vm.params.skils_truck = false;
      vm.params.reviews = false;

      vm.searchOptions.namecarrier = null;
      vm.searchOptions.type_trailer = null;
      vm.searchOptions.lenght = null;
      vm.searchOptions.weight = null;
      vm.searchOptions.size = null;
      vm.searchOptions.skills = null;
      vm.searchOptions.reviews = null;
      
      vm.reloadList();
    };

    /* Function is executed if user click "searching" tab */
    vm.searchWithParams = function() {
      vm.params.company = ($stateParams.company && $stateParams.company != null) ? $stateParams.company : false;
      vm.params.trailer_type = ($stateParams.trailer_type && $stateParams.trailer_type != null) ? $stateParams.trailer_type : false;
      vm.params.length = ($stateParams.len && $stateParams.len != null) ? $stateParams.len : false;
      vm.params.weight = ($stateParams.wei && $stateParams.wei != null) ? $stateParams.wei : false;
      vm.params.size = ($stateParams.size && $stateParams.size != null) ? $stateParams.size : false;
      vm.params.skils_truck = ($stateParams.skils_truck && $stateParams.skils_truck != null) ? $stateParams.skils_truck : false;
      vm.params.reviews = ($stateParams.reviews && $stateParams.reviews != null) ? $stateParams.reviews : false;
      
      vm.setSearchParams()
        .then(function(){
          vm.reloadList();
        });
    };

    /* look for carriers and reload table list */
    vm.loadTab = function() {
      if (vm.initializedList) {
        vm.reloadList();
      } else {
        vm.searchCarriers();
      }
    };

    /* Main function that looks for carriers and prepare table list configuration depending of rows received */
    vm.searchCarriers = function() {
      vm.datatableOptions = DTOptionsBuilder.newOptions()
                            .withOption('ajax', function(data, callback, settings){

                                // Page
                                vm.searchOptions.page = DatatableTool.getPage(data);

                                Carriers.searchCarrierByShipper(vm.searchOptions)
                                  .then(function(response){
                                    var records = DatatableTool.getRecords(response);

                                    // Info
                                    if (!angular.isUndefined(records.data)) {
                                        _.forEach(records.data, function(value, key) {

                                            value.img_perfil_carrier = null;
                                            value.carrier_company_name = null;
                                            if(angular.isDefined(value.carrier)){
                                              if(angular.isDefined(value.carrier.user) && angular.isDefined(value.carrier.user.img_perfil)){
                                                value.img_perfil_carrier = $.parseJSON('['+value.carrier.user.img_perfil+']');
                                                value.img_perfil_carrier = value.img_perfil_carrier[0];
                                              }

                                              value.carrier_company_name = value.carrier.company_name;
                                            }

                                            value.transported = null;
                                            value.transit = null;
                                            value.trucks = null;
                                            if(angular.isDefined(value.LoadsTransported)){
                                              value.transported = value.LoadsTransported;
                                            }

                                            if(angular.isDefined(value.LoadsTransit)){
                                              value.transit = value.LoadsTransit;
                                            }

                                            if(angular.isDefined(value.CantidadTruck)){
                                              value.trucks = value.CantidadTruck;
                                            }

                                            value.SkillsCarrierText = "";
                                            if(angular.isDefined(value.SkillsCarrier) && value.SkillsCarrier.length){
                                              var arr_skills = [];
                                              for(var sk in value.SkillsCarrier){
                                                arr_skills.push(value.SkillsCarrier[sk].skills_name);
                                              }
                                              value.SkillsCarrierText = arr_skills.join(', ');
                                            }
                                        });
                                    }
                                    vm.carriersList = records.data;
                                    records.data = [{img_perfil_carrier: '', carrier_company_name: '', transported: '', transit: '', trucks: '', skills: '', reviews: '', contact: '', actions: ''}];
                                    callback(records);
                                  })
                                  .catch(function (error) {
                                    vm.carriersList = [];
                                    callback(DatatableTool.getRecords(null));
                                  });
                            })
                            .withDataProp('data')
                            .withOption('processing', true)
                            .withOption('serverSide', true)
                            .withOption('searching', false)
                            .withOption('info', false)
                            .withOption('lengthChange', false)
                            .withOption('pageLength', DatatableTool.getLimit(null))
                            .withOption('responsive', true)
                            .withLanguage(DatatableTool.getDefaultLanguageSettings())
                            .withPaginationType('numbers');
      
      vm.dtColumns = [
        DTColumnBuilder.newColumn('img_perfil_carrier'),
        DTColumnBuilder.newColumn('carrier_company_name'),
        DTColumnBuilder.newColumn('transported'),
        DTColumnBuilder.newColumn('transit'),
        DTColumnBuilder.newColumn('trucks'),
        DTColumnBuilder.newColumn('skills'),
        DTColumnBuilder.newColumn('reviews'),
        DTColumnBuilder.newColumn('contact'),
        DTColumnBuilder.newColumn('actions')
      ];

      vm.loadingCarrierData = false;
      vm.initializedList = true;
    };

    /* Function reloads table list and looks for carriers again */
    vm.reloadList = function() {
        // Reload datatables
        DatatableTool.reload();
    };

    /* Show Carrier details in drop down tab */
    vm.showDetailsUnique = function(carrier_id) {
      for(var i in vm.carriersList){
        if(vm.carriersList[i].id == carrier_id){
          vm.carriersList[i].expanded = !vm.carriersList[i].expanded;
        }else{
          vm.carriersList[i].expanded = false;
        }
      }
    };

    vm.initialize();
  }
})();

'use strict';
/* Interceptor service 
 * Add token and some params in http header in each http request made in the site
 * Just in the case the user is logged
 */
angular.module('loadsAppWeb')
    .service('AuthInterceptor', function ($q, $localStorage)
    {
        
        return {
            request: function (cfg)
            {
                cfg.headers = cfg.headers || {};

                var customHeaders = {
                    headers: {
                        "Authorization": "",
                        "Content-Type": "application/json"
                    }
                };

                //Get Access Token.
                //$localStorage.$apply();
                var accessToken = $localStorage.token;
                if (accessToken) {
                    customHeaders.headers["Authorization"] = 'Bearer ' + accessToken;
                }

                //Allow us to override the Interceptor configurtion, sending the headers at a http request.
                cfg.headers = angular.extend(customHeaders.headers, cfg.headers);

                return cfg;

            },
            responseError: function(response) {
                return $q.reject(response);
            }
        };

    });

angular.module('loadsAppWeb')
    .config(['$httpProvider',
        function ($httpProvider)
        {

            $httpProvider.interceptors.push('AuthInterceptor');

        }]);

'use strict';
/* Auth services
 * Login
 * Remember password
 * Signup
 * Update profile
 * Change user pic
 * Logout
 * Functions to resolve if user is authenticated or not
 * Get and set user data and token when user log in site
 */
angular.module('loadsAppWeb')
    .service('Auth', function (Config, $q, $http, $localStorage, Upload)
    {
        this.loginByCredentials = function (params)
        {
            var q = $q.defer();

            $http.post(Config.ENV.API_ENDPOINT + 'security/auth', params)
                .then(function (response) {
                    q.resolve(response.data);
                })
                .catch(function (response) {
                    q.reject(false);
                });

            return q.promise;
        };

        this.rememberMyPassword = function (params)
        {
            var q = $q.defer();

            $http.post(Config.ENV.API_ENDPOINT + 'security/auth/rememberPassword', params)
                .then(function (response) {
                    q.resolve(response.data);
                })
                .catch(function (response) {
                    q.reject(false);
                });

            return q.promise;
        };

        this.doSignUp = function (params)
        {
            var q = $q.defer();

            $http.post(Config.ENV.API_ENDPOINT + 'user/createUser', params)
                .then(function (response) {
                    q.resolve(response.data);
                })
                .catch(function (response) {
                    q.reject(false);
                });

            return q.promise;
        };

        this.updateProfile = function (params)
        {
            var q = $q.defer();

            $http.post(Config.ENV.API_ENDPOINT + 'user/UpdateUser', params)
                .then(function (response) {
                    q.resolve(response.data);
                })
                .catch(function (response) {
                    q.reject(false);
                });

            return q.promise;
        };

        this.uploadMyPic = function(params)
        {
        // Defer
            var defer = $q.defer();
            // Url
            var url = Config.ENV.API_ENDPOINT + 'user/setMyPicImg';

            // Get
            Upload.upload({url: url, data: params})
              .then(function(response) {
                    // Success
                    console.log(response);
                    if(response.data.status == 'ok' ){
                      defer.resolve(response.data.message); 
                    }else{
                      defer.reject(false);                  
                    }
                })
                .catch(function(response) {
                    // Error

                    defer.reject(false);
                });

            return defer.promise; 
        };

        this.logout = function ()
        {
            $localStorage.$reset();

            return true;   
        };

        this.setSessionUser = function (data)
        {
            var q = $q.defer();

            if(angular.isDefined(data.userData))
            {
                $localStorage.userData = data.userData;
                $localStorage.token = data.userData.token;
            }

            if(angular.isDefined(data.Info_perfil))
            {
                $localStorage.userProfile = data.Info_perfil.userProfile;
                $localStorage.userDataProfile = data.Info_perfil.dataUser;
            }

            if(angular.isDefined(data.capabilities))
            {
                $localStorage.userCapabilities = data.capabilities;
            }

            if(angular.isDefined(data.Info_compa))
            {
                $localStorage.userCompany = data.Info_compa;
            }

            q.resolve(true);

            return q.promise;
        }

        this.isAuthenticated = function ()
        {
            var flag = false;
            if(angular.isDefined($localStorage.userData) && angular.isDefined($localStorage.userProfile) && angular.isDefined($localStorage.userCapabilities)){
                flag = true;
            }

            return flag;
        }

        this.getUserData = function ()
        {
            if(angular.isDefined($localStorage.userData)){
                return $localStorage.userData;
            }

            return false;
        }

        this.getUserDataProfile = function ()
        {
            if(angular.isDefined($localStorage.userDataProfile)){
                return $localStorage.userDataProfile;
            }

            return false;
        }

        this.getUserProfile = function ()
        {
            if(angular.isDefined($localStorage.userProfile)){
                return $localStorage.userProfile;
            }

            return false;
        }

        this.getUserInfoCompany = function ()
        {
            if(angular.isDefined($localStorage.userCompany)){
                return $localStorage.userCompany;
            }

            return false;
        }

        this.getUserToken = function ()
        {
            if(angular.isDefined($localStorage.token)){
                return $localStorage.token;
            }

            return false;
        }

        this.setAutomaticAssign = function(newValue)
        {
            var userData = this.getUserData();

            if(userData && (newValue === 1 || newValue === 0)){
                if(angular.isDefined(userData.automatic_asign) && angular.isDefined($localStorage.userData)){
                    $localStorage.userData.automatic_asign = newValue;

                    return true;
                }
            }

            return false;
        }


    });

(function() {
  'use strict';

  /* Sign up controller
   * Prepare form depending of profile choosed
   * Validate form
   * Send data to API and show successful message or any wrong*/

  angular
    .module('loadsAppWeb')
    .controller('SignUpController', SignUpController);

  /** @ngInject */
  function SignUpController($filter, toastr, $state, Config, Auth, Loads) {
	var vm = this;

    /* Function executed at start
     * Prepare the steps of the form
     * set form vars and configuration of profile select
     * Configuration of location fields
     */
	vm.initialize = function() {

		vm.formsToValidate = [{form: 'basic_info', validated: false, subtitle: ''},
							  {form: 'company_info', validated: false, subtitle: '2'}];
							  //{form: 'insurance_info', validated: false, subtitle: '3'}];
		vm.validateForm = vm.formsToValidate[0].form;
		vm.validateFormIndex = 0;
		vm.subtitleForm = vm.formsToValidate[0].subtitle;

        vm.companyCitySelect = null;
        vm.companyStateSelect = null;
        vm.companyCountrySelect = null;
		
		vm.newUser = {};
		vm.newUser.profile = null;

		//contact information
		vm.newUser.user_first_name = null;
		vm.newUser.user_last_name = null;
		vm.newUser.user_phone = null;
		vm.newUser.user_email = null;
		vm.newUser.user_password = null;

		//company information
		vm.newUser.company_name = null;
		vm.newUser.city_company = null;
		vm.newUser.state_company = null;
        vm.newUser.country_company = null;
		vm.newUser.phone_company = null;
		vm.newUser.email_company = null;
		vm.newUser.address_company = null;
        vm.newUser.type_document = null;
        vm.newUser.type_document_number = null;
        //vm.newUser.mc_number = null;
        //vm.newUser.dot_number = null;
        //campos nuevos para driver
        vm.newUser.name_company = null;
        vm.newUser.type_document_company = null;
        vm.newUser.number_type_document_company = null;
        vm.newUser.type_truck = null;
        vm.newUser.truck_skill = null;
        vm.newUser.placa = null;
        vm.newUser.department_company = null;

		//insurance information
		vm.newUser.bond_insurance_num_policy = null;
		vm.newUser.bond_insurance_comp = null;
		vm.newUser.bond_insurance_num = null;
        vm.newUser.name_bank = null;
        vm.newUser.number_account_bank = null;

		vm.profileOptions = [
							 {id: null, name: $filter('translate')('form:default:select:null_option')},
							 {id: 'CARRIER', name: $filter('translate')('header:profile:carrier:title')},
							 {id: 'GENERADOR_CARGA', name: $filter('translate')('header:profile:generador_carga:title')},
                             {id: 'OWNER', name: $filter('translate')('header:profile:owner:title')},
                             {id: 'DRIVER', name: $filter('translate')('header:profile:driver:title')}
							];

        vm.typeDocumentOptions = [
                             {id: null, name: $filter('translate')('form:default:select:null_option')},
                             {id: 'CC', name: $filter('translate')('sign_up:form:type_document:cc:label')},
                             {id: 'NIT', name: $filter('translate')('sign_up:form:type_document:nit:label')},
                             {id: 'Pasaporte', name: $filter('translate')('sign_up:form:type_document:pas:label')},
                             {id: 'CE', name: $filter('translate')('sign_up:form:type_document:ce:label')},
                             {id: 'NUIP', name: $filter('translate')('sign_up:form:type_document:nuip:label')}
                            ];

        vm.optionsGoogleInputCity = {
            types: ['(cities)']
        };

        vm.getItemsSizeLengthLoads();
	};

    /* Get load attributes */
    vm.getItemsSizeLengthLoads = function() {
      vm.itemsSizeLenLoads = {};
      Loads.getItemsSizeLengthLoads()
        .then(function(response){
          if(response){
            vm.itemsSizeLenLoads = response;
            if(angular.isDefined(vm.itemsSizeLenLoads.Trucktypes_list)){
                vm.setTruckTypesList();
            }

            if(angular.isDefined(vm.itemsSizeLenLoads.TrucksSkills_list)){
                vm.setTruckSkillsList();
            }
          }
        }); 
    };

    /* Set truck types list as local var */
    vm.setTruckTypesList = function() {
      vm.truckTypesList = [];
      if(vm.itemsSizeLenLoads.Trucktypes_list.length){
        for(var i in vm.itemsSizeLenLoads.Trucktypes_list){
          vm.truckTypesList.push({id: vm.itemsSizeLenLoads.Trucktypes_list[i].id, name: vm.itemsSizeLenLoads.Trucktypes_list[i].truck_type_name});
        }
      }
    };

    /* Set truck skills list as local var */
    vm.setTruckSkillsList = function() {
      vm.truckSkillsList = [];
      if(vm.itemsSizeLenLoads.TrucksSkills_list.length){
        for(var i in vm.itemsSizeLenLoads.TrucksSkills_list){
          vm.truckSkillsList.push({id: vm.itemsSizeLenLoads.TrucksSkills_list[i].id, name: vm.itemsSizeLenLoads.TrucksSkills_list[i].skills_name});
        }
      }
    };

    /* Catch up info about country, state and city selected */
    vm.autoCompleteStateCountry = function() {
        if(angular.isDefined(vm.companyCitySelect.address_components)) {
            for(var i in vm.companyCitySelect.address_components){
                if(vm.companyCitySelect.address_components[i].types.indexOf('administrative_area_level_1') != -1){
                    vm.companyStateSelect = vm.companyCitySelect.address_components[i].long_name;
                    console.log(vm.companyStateSelect);
                }

                if(vm.companyCitySelect.address_components[i].types.indexOf('country') != -1){
                    vm.companyCountrySelect = vm.companyCitySelect.address_components[i].long_name;
                    console.log(vm.companyCountrySelect);
                }
            }
        }
    };

    /* Send data to API and do sign up process */
	vm.doSignUp = function() {
		vm.newUser.user_password = CryptoJS.SHA1(vm.newUser.user_password.toString()).toString();

        vm.newUser.city_company = (angular.isDefined(vm.companyCitySelect.name) ? vm.companyCitySelect.name : vm.companyCitySelect.formatted_address);
        vm.newUser.state_company = (angular.isDefined(vm.companyStateSelect.name) ? vm.companyStateSelect.name : vm.companyStateSelect.formatted_address);
        vm.newUser.department_company = (angular.isDefined(vm.companyStateSelect.name) ? vm.companyStateSelect.name : vm.companyStateSelect.formatted_address);
        vm.newUser.country_company = (angular.isDefined(vm.companyCountrySelect.name) ? vm.companyCountrySelect.name : vm.companyCountrySelect.formatted_address);

        if(vm.newUser.profile == 'DRIVER'){
            vm.newUser.driver_data_app = 'CO';
        }else{
            if(angular.isDefined(vm.newUser.driver_data_app)){
                delete vm.newUser.driver_data_app;
            }
        }

		Auth.doSignUp(vm.newUser)
                .then(function(response) {
                	if(angular.isDefined(response.status) && response.status != 'error'){
                        toastr.success($filter('translate')('sign_up:form:success'));
                        $state.go('public.login');
                    }else{
                        if(response.message == "user_exist"){
                            toastr.error($filter('translate')('sign_up:form:error:user_exists'));
                        }else{
                            toastr.error($filter('translate')('sign_up:form:error'));
                        }
                    }
                })
                .catch(function(error) {
                    toastr.error($filter('translate')('sign_up:form:error'));
                });
	};

    /* Validate all step of form and allow to send the data to above function to send data to API */
	vm.validateSignup = function(userForm) {
		// Validate form.
        if (userForm.$invalid) {
            // If there was an error for required fields.
            if (userForm.$error.required) {
                toastr.error($filter('translate')('sign_up:form:required:fields:error'));
            } else {
                toastr.error($filter('translate')('sign_up:form:required:fields:invalid'));
            }
            return;
        }

        //Validations of step 2
        if(userForm.$name == 'signupFormName2'){
            if(vm.newUser.profile == 'CARRIER' || vm.newUser.profile == 'OWNER'){
                if(!vm.newUser.mc_number && !vm.newUser.dot_number){
                    toastr.error($filter('translate')('sign_up:form:required:fields:error'));
                    return;
                }

                if(!vm.newUser.ein_number && !vm.newUser.corp_number){
                    toastr.error($filter('translate')('sign_up:form:required:fields:error'));
                    return;
                }
            }

            if(vm.newUser.profile == 'GENERADOR_CARGA'){
                if(!vm.newUser.ein_number && !vm.newUser.corp_number){
                    toastr.error($filter('translate')('sign_up:form:required:fields:error'));
                    return;
                }
            }
        }

        //Validations of step 3
        if(userForm.$name == 'signupFormName3'){
            if(vm.newUser.profile == 'CARRIER' || vm.newUser.profile == 'OWNER'){
                if(!vm.newUser.bond_insurance_num_policy || !vm.newUser.bond_insurance_comp || !vm.newUser.bond_insurance_num){
                    toastr.error($filter('translate')('sign_up:form:required:fields:error'));
                    return;
                }
            }
        }

        vm.formsToValidate[vm.validateFormIndex].validated = true;

        var moreValidateForm = true
        , indexform = 0;

        //Cycle allow continuing to next step or do signup
        while(moreValidateForm){
        	if(indexform <= vm.validateFormIndex){
        		indexform++;
        	}else{
        		if(angular.isDefined(vm.formsToValidate[indexform])){
                    if(vm.newUser.profile == 'GENERADOR_CARGA' && indexform == 2){
                        vm.doSignUp();
                    }else{
                        vm.validateForm = vm.formsToValidate[indexform].form;
                        vm.validateFormIndex = indexform;
                        vm.subtitleForm = vm.formsToValidate[indexform].subtitle;
                    }
        		}else{
        			vm.doSignUp();
        		}
        		moreValidateForm = false;
        	}
        }
	};

    // Come back in step of form
    vm.comeBackForm = function () {
        if(vm.validateFormIndex > 0){
            if(angular.isDefined(vm.formsToValidate[vm.validateFormIndex - 1])){
                vm.validateFormIndex = vm.validateFormIndex - 1;
                vm.validateForm = vm.formsToValidate[vm.validateFormIndex].form;
                vm.subtitleForm = vm.formsToValidate[vm.validateFormIndex].subtitle;
            }
        }
    };

    // Function to redirect to login page
    vm.goToLogin = function () {
        $state.go('public.login');
    };

    //Call of function which is executed at start
	vm.initialize();
  }
})();

(function() {
  'use strict';

  /* Login controller
   * form validation
   * Send data to API and if response is right it'll let the user continue*/

  angular
    .module('loadsAppWeb')
    .controller('LoginController', LoginController);

  /** @ngInject */
  function LoginController($state, toastr, $filter, Config, Auth) {
	var vm = this;

    // Validate form and send data to API
	vm.doLogin = function (loginFormName)
    {
        // Valid form
        if (loginFormName.$invalid) {

            // Email
            if (!loginFormName.email.$valid && loginFormName.email.$error.required) {
                toastr.error($filter('translate')('login:email:error'));
                return;
            }

            // Password
            if (!loginFormName.password.$valid && loginFormName.password.$error.required) {
                toastr.error($filter('translate')('login:password:error'));
                return;
            }

            toastr.error($filter('translate')('login:invalid:error'));

            return;
        }

        var params = {};
        params.username = vm.credentials.email;
        params.password = CryptoJS.SHA1(vm.credentials.password.toString()).toString(); //Before to send, we excript the password in SHA! encription

        // Service Auth (app/auth/services/auth-serv)
        Auth.loginByCredentials(params)
                .then(function(response) {
                    if(angular.isDefined(response.userData) && angular.isDefined(response.Info_perfil)){
                        Auth.setSessionUser(response)
                            .then(function() {
                                $state.go('private.home');
                            });
                    }else{
                        toastr.error($filter('translate')('login:credentials:invalid'));
                    }
                })
                .catch(function(error) {
                    //console.log(error);
                });

    	
    };

    // Function executed at start 
	vm.initialize = function() {

        // Credentials
        vm.credentials = {
            email: null,
            password: null
        };
    };

    vm.initialize();
  }
})();

(function() {
  'use strict';

  /* Forgot password controller
   * form validation
   * Send data to API and receiving a response*/

  angular
    .module('loadsAppWeb')
    .controller('ForgotPasswordController', ForgotPasswordController);

  /** @ngInject */
  function ForgotPasswordController($state, toastr, $filter, Config, Auth) {
	var vm = this;

    // Validate form and send data to API
	vm.sendRequest = function (forgotPasswordFormName)
    {
        // Valid form
        if (forgotPasswordFormName.$invalid) {

            // Email
            if (!forgotPasswordFormName.email.$valid && forgotPasswordFormName.email.$error.required) {
                toastr.error($filter('translate')('forgot_password:email:error'));
                return;
            }

            toastr.error($filter('translate')('forgot_password:invalid:error'));

            return;
        }

        var params = {};
        params.email = vm.data.email;

        // Service Auth (app/auth/services/auth-serv)
        Auth.rememberMyPassword(params)
                .then(function(response) {
                    if(angular.isDefined(response.status) && response.status == "ok"){
                        toastr.success($filter('translate')('forgot_password:request:success'));
                        $state.go('private.home');
                    }else{
                        toastr.error($filter('translate')('forgot_password:data:invalid'));
                    }
                })
                .catch(function(error) {
                    //console.log(error);
                });

    	
    };

	vm.initialize = function() {

        // Forgot Password data
        vm.data = {
            email: null
        };
    };

    vm.initialize();
  }
})();

